/**
 *   Name: Bizagi Workportal Desktop Navigator Controller
 *   Author: Mauricio Snchez - Alexander Mejia
 */
bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.navigator", {
  CONTEXTS: {
    ACTIVITY: "ACTIVITY",
    EDIT_ACTIVITY: "EDITACTIVITY",
    ACTIVITY_PLAN: "ACTIVITYPLAN",
    HOME: "HOME",
    TEMPLATE_ENGINE_VIEW: "TEMPLATEENGINE-VIEW",
    PLANS_VIEW: "PLANS-VIEW",
    PLAN_ACTIVITIES: "PLANACTIVITIES",
    CASES_TEMPLATE_VIEW: "CASES-TEMPLATE-VIEW" },

  CLASSES: {
    GRID_CONTAINER: "ui-bizagi-wp-project-grid-container" } },

{
  /**
   *   Constructor
   */
  init: function init(workportalFacade, dataService, accumulatedcontext, params) {
    var self = this;
    // Call base
    self.workportalFacade = workportalFacade;
    self.accumulatedcontext = accumulatedcontext;
    self.dataService = dataService;
    self._super(workportalFacade, dataService, params);

    // Params
    self.flagDisplayDate = true;
    self.printFormCommand = new bizagi.workportal.command.printform(self.workportalFacade);
    self.fromClickBreadCrumb = false;
    self.showReleaseButton = false;
    self.caseLink = params && params.caseLink ? params.caseLink : false;
    self.taskOptionsWidget = new bizagi.workportal.widgets.task.options(workportalFacade, dataService, params);

    // Used to save parameters from the form
    self.onRenderFinishParams = null;

    //Load templates
    self.loadTemplates({
      "navigator": bizagi.getTemplate("bizagi.workportal.desktop.widget.navigator").concat("#navigator-wrapper"),
      "child": bizagi.getTemplate("bizagi.workportal.desktop.widget.navigator").concat("#navigator-child"),
      "popup-release": bizagi.getTemplate("bizagi.workportal.desktop.widget.navigator").concat("#navigator-popup-release") });

    self.accumulatedcontext = accumulatedcontext;

    self.releaseDialogBox = {};
  },

  /**
      * Renders the template defined in the widget
      */
  renderContent: function renderContent() {
    var self = this;
    var template = self.getTemplate("navigator");
    self.content = template.render($.extend(self.getDataObj(), { security: bizagi.menuSecurity }));
    bizagi.navigator = bizagi.navigator || { info: {} };
    self.info = bizagi.navigator.info;
    self._level = Object.keys(self.info).length - 1;
    $(".navbar-print", self.content).on("click", $.proxy(self.printCase, self));
    $(".navbar-back", self.content).on("click", $.proxy(self.goToBack, self));
    $(".navbar-assignation-everyone > .ae-action", self.content).on("click", $.proxy(self.actionButtonEvent, self));
    self.releaseDialogBox.formContent = self.getTemplate("popup-release").render({});

    return self.content;
  },
  /**
      * links events with handlers
      */
  postRender: function postRender() {
    var self = this;
    var content = self.getContent();
    var $container = content.find("ul");
    self.sub('ON_RENDER_FINISH', $.proxy(self.onRenderFinish, self));
    self.sub("NAVIGATE", $.proxy(self.refresh, self));
    self.sub("UPDATE_LASTCRUMBPARAMS_INFO", $.proxy(self.updateLastCrumbParamsInfo, self));
    self.sub("NAVIGATOR_GETLEVEL", function () {
      return self.getLevel();
    });
    self.sub("NAVIGATOR_BACK", $.proxy(self.goToPreviousCrumb, self));
    $container.on("click", "li", $.proxy(self.crumbEvent, self));

    self.plugins = {};
    self.plugins.popupRelease = {
      form: {
        buttonAccept: $("#button-accept-release", self.releaseDialogBox.formContent),
        buttonCancel: $("#button-cancel-release", self.releaseDialogBox.formContent) } };



    self.plugins.popupRelease.form.buttonAccept.on("click", $.proxy(self.onClickAcceptRelease, self));
    self.plugins.popupRelease.form.buttonCancel.on("click", $.proxy(self.onClickCancelRelease, self));
    //Notification service
    self.notifier = bizagi.injector.get('notifier');
  },

  /**
      * Called from the event when the form is rendered
      * @param {subscription} event 
      * @param {form} params 
      */
  onRenderFinish: function onRenderFinish(event, params) {
    var self = this;
    self.onRenderFinishParams = params;
    self.taskOptionsWidget.renderEnds();
  },

  getBreadCrumbInfo: function getBreadCrumbInfo() {
    return bizagi.navigator.info;
  },

  setBreadCrumbInfo: function setBreadCrumbInfo(info) {
    bizagi.navigator.info = info;
  },

  drawBreadCrumb: function drawBreadCrumb(breadCrumbInfo, showSteps) {
    var self = this;
    $(self.content).find("ul").html('');
    if (showSteps) {
      for (var i = -1; i < Object.keys(breadCrumbInfo).length - 1; i += 1) {
        var crumbItem = breadCrumbInfo[i];
        crumbItem.surrogateKey = i;
        var crumbContent = self.drawCrumb(crumbItem);
        $(self.content).find("ul").append(crumbContent);
      }
    }
  },

  drawCrumb: function drawCrumb(itemCrumb) {
    var self = this;
    var name = itemCrumb.args.histName;
    var isPrincipalHome = false;
    var template = self.getTemplate("child");
    var child = template.render({
      surrogateKey: itemCrumb.surrogateKey,
      name: name,
      isPrincipalHome: isPrincipalHome });

    return child;
  },
  crumbEvent: function crumbEvent(event) {
    var self = this;
    var itemCrumb = self.info[$(event.target).data("surrogatekey")];
    self.goToCrumb(itemCrumb, event);
  },

  goToCrumb: function goToCrumb(crumb, event) {
    var self = this;
    if (crumb) {
      var args = crumb.args;
      var skipRefresh = false;
      var contextActivity = this.getParentContext(crumb.context);
      if (contextActivity == "TEMPLATEENGINE-VIEW" || contextActivity == "SEARCH-ENGINE-VIEW") {
        skipRefresh = true;
        if (event && event.target) {
          var index = parseInt($(event.target).data("surrogatekey"), 10) + 1;
          self.accumulatedcontext.cutContext(index);
        }
      }
      this.deleteRemainingCrumbs(crumb);
      self.pub("notify", {
        skipRefresh: skipRefresh,
        type: contextActivity,
        args: $.extend(args, { showContextByMenuDashboard: contextActivity }) });

    }
  },

  goToPreviousCrumb: function goToPreviousCrumb() {
    var self = this;
    var pathSize = Object.keys(self.info).length;
    var crumbBack = self.info[pathSize - 3];
    if (!crumbBack) {
      crumbBack = self.info[pathSize - 2];
    }
    self.goToCrumb(crumbBack);
  },

  deleteRemainingCrumbs: function deleteRemainingCrumbs(itemCrumb) {
    var self = this;
    $.each(self.info, function (key) {
      if (itemCrumb.surrogateKey < parseInt(key, 10)) {
        delete self.info[key];
      }
    });
  },

  getParentContext: function getParentContext(context) {
    if (context === "OVERVIEW" || context === "COMMENTS" || context === "FILES" || context === "TIMELINE") {
      return "ACTIVITY";
    } else
    if (context === "ACTIVITYPLANOVERVIEW" || context === "ACTIVITYPLANCOMMENTS" || context === "ACTIVITYPLANFILES" || context === "ACTIVITYPLANTIMELINE") {
      return "ACTIVITYPLAN";
    } else
    if (context === "PLANSIDEBAR" || context === "EDITACTIVITY" || context === "PLANACTIVITIES" || context === "PLANCOMMENTS" || context === "PLANFILES" || context === "PLANTIMELINE") {
      return "PLANACTIVITIES";
    }
    return context;
  },

  resetBreadCrumb: function resetBreadCrumb(params) {
    var self = this;
    self.info = {};
    self.info[-1] = self.copyDataObjectWithoutReference(params);
  },

  updateInfo: function updateInfo(params) {
    var self = this;
    var pathSize = Object.keys(self.info).length;
    if (params.args.level && params.args.level < pathSize && params.args.level > 0) {
      for (var j = params.args.level - 1; j < pathSize - 1; j += 1) {
        delete self.info[j];
      }
      pathSize = params.args.level;
    }

    var lastCrumb = self.info[pathSize - 2];
    var secondLastCrumb = self.info[pathSize - 3];
    if (params.context == "HOME" || params.context === "SEARCH-ENGINE-VIEW" && typeof params.args.reference === "undefined") {//reset breadCrumb
      this.resetBreadCrumb(params); //clean breadcrumb and add new item with entity name
    } else
    if (params.context == "ACTIVITY" || params.context == "ACTIVITYPLAN") {
      if (secondLastCrumb && self.getParentContext(params.context) == self.getParentContext(secondLastCrumb.context)) {
        self.cleanBreadCumb(secondLastCrumb, params, 3);
      } else if (lastCrumb && self.getParentContext(params.context) == self.getParentContext(lastCrumb.context)) {
        self.cleanBreadCumb(lastCrumb, params, 2);
      } else {
        self.info[pathSize - 1] = self.copyDataObjectWithoutReference(params);
      }
    } else
    if (params.context == "TEMPLATEENGINE-VIEW" || params.context === "SEARCH-ENGINE-VIEW") {
      var firstCrumb = self.info[-1];
      if (firstCrumb && firstCrumb.context != "TEMPLATEENGINE-VIEW" &&
      firstCrumb.context != "SEARCH-ENGINE-VIEW" &&
      firstCrumb.context != "HOME") {
        self.info = {};
        pathSize = Object.keys(self.info).length;
        lastCrumb = null;
      }
      if (params.args.referenceType == "FACT" || params.args.referenceType == "VIRTUAL") {
        var factCrumbItem = self.hasGuidEntityInBreadCrumb(params.args.guidEntityCurrent);
        var newFactCrumbItem = self.copyDataObjectWithoutReference(params);
        if (params.args.level == 1) {
          self.info = {};
          self.info[-1] = newFactCrumbItem;
        } else
        if (lastCrumb && lastCrumb.args && lastCrumb.args.level == params.args.level) {
          self.cleanBreadCumb(lastCrumb, params, 2);
        } else if (!factCrumbItem) {
          self.info[pathSize - 1] = newFactCrumbItem;
        } else {
          newFactCrumbItem.surrogateKey = factCrumbItem.surrogateKey;
          self.info[newFactCrumbItem.surrogateKey] = newFactCrumbItem;
          self.deleteRemainingCrumbs(newFactCrumbItem);
        }
      } else if (params.args.referenceType == "ENTITY") {
        if (params.args.eventType != "DATA-NAVIGATION" && !self.hasXpathInBreadCrumb(params.args.xpath)) {
          self.info[pathSize - 1] = self.copyDataObjectWithoutReference(params);
        }
      }
    } else
    {
      if (lastCrumb && self.getParentContext(params.context) == self.getParentContext(lastCrumb.context)) {
        self.cleanBreadCumb(lastCrumb, params, 2);
      } else {
        self.info[pathSize - 1] = self.copyDataObjectWithoutReference(params);
      }
    }
    self.setBreadCrumbInfo(self.info);
  },

  updateLastCrumbParamsInfo: function updateLastCrumbParamsInfo(event, params) {
    var self = this;
    var pathSize = Object.keys(self.info).length;
    if (pathSize > 0) {
      $.extend(self.info[pathSize - 2].args, self.copyDataObjectWithoutReference(params.args));
    }
  },

  hasXpathInBreadCrumb: function hasXpathInBreadCrumb(xpath) {
    var self = this;
    for (var i = -1; i < Object.keys(bizagi.navigator.info).length - 1; i += 1) {
      var crumbItem = bizagi.navigator.info[i];
      if (crumbItem.args.xpath == xpath) {
        return true;
      }
    }
    return false;
  },

  hasGuidEntityInBreadCrumb: function hasGuidEntityInBreadCrumb(guidEntity) {
    var self = this;
    for (var i = -1; i < Object.keys(bizagi.navigator.info).length - 1; i += 1) {
      var crumbItem = bizagi.navigator.info[i];
      if (crumbItem.args.guidEntityCurrent == guidEntity) {
        return crumbItem;
      }
    }
    return false;
  },

  cleanBreadCumb: function cleanBreadCumb(crumb, params, steps) {
    var self = this;
    var pathSize = Object.keys(self.info).length;
    crumb = self.copyDataObjectWithoutReference(params);
    self.info[pathSize - steps] = crumb;
    this.deleteRemainingCrumbs(crumb);
  },

  drawDateNavigator: function drawDateNavigator(params) {
    var self = this;
    var currentLevel = Object.keys(self.info).length;
    var showDateNavigator = currentLevel == 1;
    if (params.args.showDateNavigator === false) {
      showDateNavigator = false;
    }
    self.onDisplayDate(showDateNavigator);
  },

  /**
      * Adjusts for home-navbar (back, print, etc.)
      * @param {JSON} params 
      */
  homeNavBarAdjust: function homeNavBarAdjust(params) {
    if (params.context == "HOME") {
      $("#ui-bizagi-wp-project-homeportal-main").addClass("disabled-right-sidebar");
      $("#ui-bizagi-wp-project-homeportal-main").removeClass("enabled-right-sidebar");
    }
  },

  setEnabledLeftSidebar: function setEnabledLeftSidebar(params) {
    if (params.data && params.data.belongs) {
      if (params.data.belongs.toLeftSidebarCD) {
        $("#ui-bizagi-wp-project-homeportal-main").addClass("enabledLeftSidebar");
      } else
      {
        $("#ui-bizagi-wp-project-homeportal-main").removeClass("enabledLeftSidebar");
      }
    }
  },

  setEnabledRightSidebar: function setEnabledRightSidebar(params) {
    var self = this;
    if (params.data && params.data.belongs) {
      if (params.data.belongs.toRightSidebarCD) {
        $("#ui-bizagi-wp-project-homeportal-main").removeClass("disabled-right-sidebar");
        $("#ui-bizagi-wp-project-homeportal-main").addClass("enabled-right-sidebar");
      } else
      {
        $("#ui-bizagi-wp-project-homeportal-main").removeClass("enabled-right-sidebar");
        $("#ui-bizagi-wp-project-homeportal-main").addClass("disabled-right-sidebar");
      }
    }
  },

  //Show back button only in activity or plan context
  setEnabledBreadCrumb: function setEnabledBreadCrumb(params) {
    var self = this;
    if (params.context == "ACTIVITY" || params.context == "ACTIVITYPLAN") {
      self.setEnabledBackButton();
      $(".nav-items-ctn", self.content).hide();
      $(".nav-items-ctn", self.content).removeClass("toLeft");
      $(".nav-items-ctn", self.content).addClass("clearfix");
    } else
    {
      $(".navbar-back", self.content).hide();
      $(".nav-items-ctn", self.content).show();
      $(".nav-items-ctn", self.content).addClass("toLeft");
      $(".nav-items-ctn", self.content).removeClass("clearfix");
    }
  },

  /**
      * add or remove the class to content wrapper for navbar
      */
  _addGridContainerClass: function _addGridContainerClass(toAdd) {
    var self = this,
    _$contentWrapper = self.getContent().closest("#content-wrapper");

    if (toAdd) {
      _$contentWrapper.addClass(self.Class.CLASSES.GRID_CONTAINER);
    } else {
      _$contentWrapper.removeClass(self.Class.CLASSES.GRID_CONTAINER);
    }
  },

  /**
      * add properties to DOM if the context is TEMPLATEENGINE-VIEW
      * @param {JSON} params 
      */
  setPropertiesForEngineTemplate: function setPropertiesForEngineTemplate(params) {
    var self = this,
    _CONTEXTS = self.Class.CONTEXTS,
    _contextsForProjectGrid = [
    _CONTEXTS.TEMPLATE_ENGINE_VIEW,
    _CONTEXTS.CASES_TEMPLATE_VIEW],

    _needsProjectGrid =
    _contextsForProjectGrid.indexOf(params.context) !== -1;

    self._addGridContainerClass(_needsProjectGrid);
  },

  /**
      * Hide back button if from caseLink, only one time.
      */
  setEnabledBackButton: function setEnabledBackButton() {
    var self = this;
    if (self.caseLink === true) {
      self.caseLink = false;
      $(".navbar-back", self.content).hide();
    } else
    {
      $(".navbar-back", self.content).show();
    }
  },

  /**
      * Return actual level.
      */
  getLevel: function getLevel() {
    var self = this;
    return Object.keys(self.info).length;
  },

  refresh: function refresh(ev, params) {
    var self = this;
    self.params = params.args;
    this.homeNavBarAdjust(params);
    this.setEnabledLeftSidebar(params);
    this.setEnabledRightSidebar(params);
    this.setEnabledBreadCrumb(params);
    this.setPropertiesForEngineTemplate(params);
    if (params.context == "ACTIVITY" || params.context == "ACTIVITYPLAN") {
      params.args.histName = self.getActivityName(params);
    }

    self.setStateBarNavigator(params.context);

    if (!params.skipRefresh) {
      if (params.args.histName && params.args.histName != "") {
        self.updateInfo(params);
      }
    }
    var showSteps = params.args.eventType != "DATA-NAVIGATION";
    self.drawBreadCrumb(self.info, showSteps);
    self.drawDateNavigator(params);
  },
  goToBack: function goToBack() {
    var self = this;
    bizagi.workportal.widgets.backnavigator.GO_TO_BACK(self);
  },

  getActivityName: function getActivityName(params) {
    var activityName;
    var idWorkItem = params.args.idWorkitem;
    for (var i = 0; i < params.args.currentState.length; i++) {
      var state = params.args.currentState[i];
      if (state && state.idWorkItem == idWorkItem) {
        if (state.displayName) {
          activityName = state.displayName;
        } else
        if (state.state) {
          activityName = state.state;
        }
        break;
      }
    }
    return activityName; //+ '-' + params.args.radNumber;
  },

  /**
      *
      * @param event
      * @param params
      */
  setStateBarNavigator: function setStateBarNavigator(context) {
    var self = this;
    if (context == "ACTIVITY") {
      self.taskOptions();
    } else {
      self.getButtonForActivityAction().hide();
    }
    if (context == "ACTIVITY" || context == "ACTIVITYPLAN") {
      self.showButtonPrintForm(true);
      self.showUrlHelp(true);
      self.flagDisplayDate = false;
      self.onDisplayDate(false);
    } else
    {
      self.showButtonPrintForm(false);
      self.showUrlHelp(false);
      self.flagDisplayDate = true;
      self.onDisplayDate(true);
    }
  },

  getDataObj: function getDataObj() {
    var self = this,
    regional = self.getResource("datePickerRegional"),
    currentDate = new Date(),
    currentDay = regional.dayNames[currentDate.getDay()],
    currentMonth = regional.monthNames[currentDate.getMonth()];

    return {
      "date": currentDay + ", " + currentMonth + " " + currentDate.getDate() + ", " + currentDate.getFullYear() };

  },

  /**
      * Copy object by value, not reference
      */
  copyDataObjectWithoutReference: function copyDataObjectWithoutReference(params) {
    return $.extend(true, {}, params);
  },
  /**
      *
      * @param show
      */
  showButtonPrintForm: function showButtonPrintForm(show) {
    var self = this;
    var $content = self.getContent();
    var $wrapperButtonPrint = $content.find(".navbar-print");

    if (show === true) {
      $wrapperButtonPrint.show();
    } else
    {
      $wrapperButtonPrint.hide();
    }
  },
  showUrlHelp: function showUrlHelp(show) {
    var self = this;
    var $content = self.getContent();
    var $wrapperUrlHelp = $content.find(".navbar-help");
    if (show === true) {
      var helpUrl = "";
      for (var i = 0; i < self.params.currentState.length; i++) {
        if (self.params.currentState[i].idTask === self.params.idTask) {
          helpUrl = bizagi.util.validateUrlWellFormed(self.params.currentState[i].helpUrl);
        }
      }
      if (!bizagi.util.isEmpty(helpUrl)) {
        $wrapperUrlHelp.show();

        $(".navbar-help a").attr("href", helpUrl);
      }
    } else
    {
      $wrapperUrlHelp.hide();
    }
  },
  /**
      *
      */
  printCase: function printCase() {
    var self = this;
    var printParams = {
      idCase: self.params.idCase,
      idWorkitem: self.params.idWorkitem,
      idTask: self.params.idTask,
      fileName: self.params.caseNumber + "_" + self.params.process };


    self.printFormCommand.print(printParams);
  },

  /**
      * validate if is necessary to show release or take ownership buttons
      */
  taskOptions: function taskOptions() {
    var self = this,
    buttonOption = self.getButtonForActivityAction(),
    buttonContainer = buttonOption.parent(),
    textForButton;
    $.when(self._taskOptionsRequest()).done(function (data) {
      buttonContainer.removeClass('nae--loading');
      textForButton = data.showRelease ? self.getResource('render-actions-release') : self.getResource('render-actions-take-ownership');
      buttonOption.text(textForButton);
      self.showReleaseButton = data.showRelease;
      if (data.showRelease === true || data.showTakeOwnership === true) {
        buttonOption.show();
        buttonContainer.show();
      } else buttonOption.hide();
    });
  },

  /**
      * make request for taskOptions
      * and on done resolve _waitShowCancelCase promise with showCancelCase value
      * @returns promise of TaskOptions request
      */
  _taskOptionsRequest: function _taskOptionsRequest() {
    var self = this,
    _done = function _done(result) {
      if (self._waitShowCancelCase !== undefined)
      self._waitShowCancelCase.resolve(result.showCancelCase);
    },
    _request = self.taskOptionsWidget.getTaskOptions(self.params.idCase, self.params.idWorkitem);

    _request.done(_done);
    return _request;
  },

  /***
      * create a new promise for wait the result of endpoint taskOptions
      * @returns promise
      */
  getShowCancelCase: function getShowCancelCase() {
    var self = this;
    self._waitShowCancelCase = new $.Deferred();
    return self._waitShowCancelCase;
  },

  /**
      * return the action button for release or takeOwnership
      */
  getButtonForActivityAction: function getButtonForActivityAction() {
    var self = this,
    content = self.getContent(),
    buttonContainer = $(".navbar-assignation-everyone", content);
    return buttonContainer.find(".ae-action");
  },

  /**
      * trigger release or takeOwnership action
      */
  actionButtonEvent: function actionButtonEvent() {
    var self = this,
    onCloseReleaseDialogBox = function onCloseReleaseDialogBox() {
      self.destroyReleaseDialogBox();
    };

    $(".navbar-assignation-everyone", self.getContent()).addClass('nae--loading');
    if (self.showReleaseButton) {
      self.releaseDialogBox.formContent.dialog({
        resizable: false,
        draggable: false,
        height: "auto",
        width: "660px",
        modal: true,
        title: self.getResource("workportal-widget-dialog-box-release-ok"),
        maximize: true,
        close: onCloseReleaseDialogBox });

    } else {
      self.actionTakeOwnership();
    }
  },

  /**
      * destroy correctly the release confirmation modal
      */
  destroyReleaseDialogBox: function destroyReleaseDialogBox() {
    var self = this,
    modal = self.releaseDialogBox.formContent;
    $(".navbar-assignation-everyone", self.getContent()).removeClass('nae--loading');
    modal.dialog("destroy");
    modal.detach();
  },

  /**
      * Triggered when user click over accept button and validate autoSave
      */
  onClickAcceptRelease: function onClickAcceptRelease() {
    var self = this,
    done = function done() {
      $(document).data('auto-save', '');
      self.launchReleaseActivity();
    },
    fail = function fail() {
      self.destroyReleaseDialogBox();
    };
    $.when(bizagi.util.autoSave()).done(done).fail(fail);
  },

  /**
      * method called after autoSave is done
      */
  launchReleaseActivity: function launchReleaseActivity() {
    var self = this;
    var params = {
      idCase: self.params.idCase,
      idWorkItem: self.params.idWorkitem };


    self.releaseDialogBox.formContent.dialog("destroy");
    self.releaseDialogBox.formContent.detach();

    self.onRenderFinishParams.args.form.dataService.multiactionService.disableRequests();
    $.when(self.validateIfRenderingIsCallingAEndpoint()).done(function () {
      $.when(self.dataService.releaseActivity(params)).done(function (data) {
        var status = data.status ? data.status : "";
        switch (status) {
          case "Success":
            self.taskReleased();
            break;

          case "ConfigurationError":
            self.showMessageAndRefreshContext("workportal-widget-dialog-box-release-configuration-error-message", params);
            break;

          default:
            self.showMessageAndRefreshContext("workportal-widget-dialog-box-release-error-message", params);
            break;}

        self.waitForLoadCompletely();
      }).fail(function () {
        self.showMessageAndRefreshContext("workportal-widget-dialog-box-release-error-message", params);
        self.waitForLoadCompletely();
      });
    });
  },

  /**
      * Show message, when user clicks on ok button, refresh the context
      * @param {string} keyResourceDescriptionMessage 
      * @param {object} params 
      */
  showMessageAndRefreshContext: function showMessageAndRefreshContext(keyResourceDescriptionMessage, params) {
    var self = this;
    var message = self.getResource(keyResourceDescriptionMessage).replace("{0}", params.idWorkItem);
    $.when(bizagi.showMessageBox(message, self.getResource("workportal-widget-dialog-box-release-error"), "error", false)).
    done(function () {self.refreshActivityContext();});
  },

  /**
      * Returns always a resolve when the request is successful or not
      * @returns deferred multiaction service in the form 
      */
  validateIfRenderingIsCallingAEndpoint: function validateIfRenderingIsCallingAEndpoint() {
    var self = this;
    var deferred = $.Deferred();

    $.when(self.onRenderFinishParams.args.form.dataService.multiactionService.validateIfBatchIsExecuting())
    // we only need to know when finishing the request with error or not
    .always(function () {
      deferred.resolve();
    });
    return deferred.promise();
  },

  /**
      * Refresh the context (all widgets in that context)
      */
  refreshActivityContext: function refreshActivityContext() {
    var self = this;
    self.pub("notify", {
      type: self.params.showContextByMenuDashboard,
      args: $.extend(self.params, { refreshAllWidgets: true }) });

  },

  /**
      * Triggered when user click over accept button
      */
  actionTakeOwnership: function actionTakeOwnership() {
    var self = this;
    var params = {
      idCase: self.params.idCase,
      idWorkItem: self.params.idWorkitem };

    $.when(self.dataService.takeOwnershipActivity(params)).done(function (data) {

      var status = data.status ? data.status : "";
      switch (status) {
        case "Success":
          // Refresh case
          self.pub("notify", {
            type: self.params.showContextByMenuDashboard,
            args: $.extend(self.params, { refreshAllWidgets: true }) });


          break;

        default:
          var _defaultMessage = self.getResource("workportal-widget-dialog-box-takeownership-error-message").replace("{0}", params.idWorkItem),
          _message = data.message ? data.message : _defaultMessage;
          bizagi.showMessageBox(_message, self.getResource("workportal-general-error-page-title"), "error", false);
          break;}

      self.waitForLoadCompletely();

    }).fail(function () {self.waitForLoadCompletely();});
  },

  /**
      * on task released send to inbox with toast notification
      */
  taskReleased: function taskReleased() {
    var self = this;
    //go to inbox
    self.publish("changeWidget", {
      widgetName: bizagi.util.defaultWidget() });

    var options = {
      closeButton: true,
      onclick: function onclick() {
        self.publish("executeAction", {
          action: bizagi.workportal.actions.action.BIZAGI_WORKPORTAL_ACTION_ROUTING,
          idCase: self.params.idCase,
          idWorkItem: self.params.idWorkitem,
          idWorkflow: self.params.idWorkflow,
          idTask: self.params.idTask,
          referrer: undefined });

      },
      timeOut: 10000 };

    self.notifier.showSucessMessage(bizagi.localization.getResource("workportal-widget-dialog-box-release-configuration-success-message"), self.params.histName, options);
  },

  waitForLoadCompletely: function waitForLoadCompletely() {
    var self = this;
    var content = self.content;
    var fn = function fn() {
      var ol = $('body #loadModuleStatus > *');
      var il = $(content).closest('.content-portal').find('#home-content').find('.ui-bizagi-loading-message');
      if (ol.length === 0 && il.length === 0) {
        $(".navbar-assignation-everyone", content).removeClass('nae--loading');
      } else setTimeout(function () {
        $(".navbar-assignation-everyone", content).addClass('nae--loading');
        fn();
      }, 100);
    };
    fn();
  },

  /**
         * Close dialog release case
         */
  onClickCancelRelease: function onClickCancelRelease() {
    var self = this;
    $(".navbar-assignation-everyone", self.getContent()).removeClass('nae--loading');
    self.releaseDialogBox.formContent.dialog("destroy");
    self.releaseDialogBox.formContent.detach();
  },
  /**
      * Hides the date displayed in the widget
      */
  onDisplayDate: function onDisplayDate(show) {
    var self = this,
    $content = self.getContent(),
    $date = $content.find(".navbar-date");

    if (show && self.flagDisplayDate) {
      $date.show();
    } else
    {
      $date.hide();
    }
  },

  /**
      * Clean widget
      */
  clean: function clean() {
    var self = this;
    var $content = self.getContent();
    if ($content) {
      var $container = $content.find("ul");

      self.unsub('ON_RENDER_FINISH', $.proxy(self.onRenderFinish, self));
      self.unsub("UPDATE_LASTCRUMBPARAMS_INFO");
      self.unsub("NAVIGATE");
      self.unsub("NAVIGATOR_GETLEVEL");
      self.unsub("NAVIGATOR_BACK");
      $container.off("click", "li");

      $(".navbar-print", self.content).off("click", $.proxy(self.printCase, self));
    }
  } });


bizagi.injector.register("bizagi.workportal.widgets.navigator", ["workportalFacade", "dataService", "accumulatedcontext", bizagi.workportal.widgets.navigator]);