bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.sessionTimeoutExpired", {}, {
  /**
                                                                                                  * Constructor
                                                                                                  */
  init: function init(workportalFacade, dataService, notifier, params) {
    var self = this;

    // Call base
    self._super(workportalFacade, dataService, params);

    //Load template
    self.loadTemplates({
      "session-timeout-expired": bizagi.getTemplate("bizagi.workportal.desktop.widget.sessionTimeoutExpired").concat("#session-timeout-expired") });

  },

  renderContent: function renderContent() {
    var self = this;
    var template = self.getTemplate('session-timeout-expired');
    self.content = template.render({});
    return self.content;
  },

  postRender: function postRender() {
    var self = this;
    self._onClickMinValue;

    // When the user clicks on the modal button reset the countDown
    $('button', self.content).click(
    function () {
      bizagi.services.redirectToLogoutPage();
    });


    // Remove the x of the modal
    self.renderingDeferred.then($.proxy(self.removeXClose, self));
  },

  /**
      * Remove the x of the modal, whe don't need it here
      * We don't want the possibility to close the modal
      */
  removeXClose: function removeXClose() {
    var self = this;
    setTimeout(function () {
      $(self.content).closest('.ui-dialog').find('.ui-dialog-titlebar-close').hide();
    }, 0);
  },

  clean: function clean() {
    var self = this;
  } });


bizagi.injector.register('bizagi.workportal.widgets.sessionTimeoutExpired', ['workportalFacade', 'dataService', 'notifier', bizagi.workportal.widgets.sessionTimeoutExpired]);