bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.sessionTimeout", {}, {
  /**
                                                                                           * Constructor
                                                                                           */
  init: function init(workportalFacade, dataService, notifier, params) {
    var self = this;

    // Call base
    self._super(workportalFacade, dataService, params);

    //Load template
    self.loadTemplates({
      "session-timeout": bizagi.getTemplate("bizagi.workportal.desktop.widget.sessionTimeout").concat("#session-timeout") });


    // Have an instance of the session timeout
    self.sessionTimeoutService = bizagi.injector.get('sessionTimeOutService');
  },

  renderContent: function renderContent() {
    var self = this;
    var template = self.getTemplate('session-timeout');
    self.content = template.render({});
    return self.content;
  },

  postRender: function postRender() {
    var self = this;
    self._onClickMinValue;

    // When the user clicks on the modal button reset the countDown
    $('button', self.content).click(
    $.proxy(function () {
      // Keep the session opened
      self.sessionTimeoutService.keepSessionOpen();
      $(this.content).closest('.ui-dialog').find('.ui-dialog-titlebar-close').trigger('click');
    },
    self));


    // Remove the x of the modal
    self.renderingDeferred.then($.proxy(self.removeXClose, self));
  },

  /**
      * Remove the x of the modal, whe don't need it here
      * We don't want the possibility to close the modal
      */
  removeXClose: function removeXClose() {
    var self = this;
    setTimeout(function () {
      $(self.content).closest('.ui-dialog').find('.ui-dialog-titlebar-close').hide();
    }, 0);
  },

  clean: function clean() {
    var self = this;
  } });


bizagi.injector.register('bizagi.workportal.widgets.sessionTimeout', ['workportalFacade', 'dataService', 'notifier', bizagi.workportal.widgets.sessionTimeout]);