  /**
      * Get entity values from server
      * 
      * @param {*} request 
      * @param {*} response 
      * @param {*} scope 
      */
  getEntityValues: function getEntityValues(request, response, scope) {
    scope.entityValuesCache = scope.entityValuesCache || [];

    if (request.term in scope.entityValuesCache) {
      response(scope.entityValuesCache[request.term]);
      return;
    }

    var entityValues = [];
    $.when(scope.dataService.getEntityValuesForInboxFilter({ entityGuid: scope.entityGuid, wildcard: request.term })).done(function (data) {
      $.grep(data, function (item) {
        entityValues.push({
          label: item.displayName,
          value: item.displayName,
          id: item.id });

      });
      scope.entityValuesCache[request.term] = entityValues;
      response(entityValues);
    });
  },

  loadUserTasks: function loadUserTasks() {
    var self = this;
    if (self.userTasksCache) {
      return self.userTasksCache;
    }

    var values = [];
    if (self.wfClassList) {
      $.when(self.dataService.getUserTasksByWFClass(self.wfClassList)).done(function (data) {
        $.grep(data, function (item) {
          values.push({
            label: item.displayName,
            value: item.displayName,
            internalId: item.internalId,
            processName: item.processName,
            id: item.id });

        });
        self.userTasksCache = values;
      });
    } else
    {
      var smartInboxId = self.params.selectedInboxData ? self.params.selectedInboxData.smartInboxId : undefined;
      var id = self.params.workflowId ? self.params.workflowId : self.params.smartInboxId || smartInboxId;
      var isSmartInbox = self.params.smartInboxId || smartInboxId !== undefined;
      $.when(self.dataService.getUserTasksForInboxFilter({ id: id, isSmartInbox: isSmartInbox })).done(function (data) {
        $.grep(data, function (item) {
          values.push({
            label: item.displayName,
            value: item.displayName,
            internalId: item.internalId,
            processName: item.processName,
            id: item.id });

        });
        self.userTasksCache = values;
      });
    }
  },

  buildInOperatorNotEntityValueField: function buildInOperatorNotEntityValueField(params) {
    var self = this;
    var template = self.inOperatorTextTemplate;
    var multiselectText = $.tmpl(template, {}).appendTo(params.valueField);
    multiselectText.tagsinput("items", {
      allowDuplicates: false,
      itemText: function itemText(item) {
        if (params.item.type == "Money") {
          var item = self.removeMoneyExtraDecimals(item);
          return self.formatCurrency(item);
        }
        return this.itemValue(item);
      },
      confirmKeys: [13],
      delimiter: null });

    multiselectText.on('beforeItemAdd', function (event) {
      var values = multiselectText.tagsinput("items");

      if (params.isNumber) {
        if (event.item.length > 0 && self.isInvalidNumber(event.item, params.item.type)) {
          event.cancel = true;
          return;
        }
      }

      if (values.length >= self.maxInValues) {
        self.notifier.showErrorMessage(bizagi.localization.getResource("workportal-widget-smartinbox-filters-error-in-values-limit", "You have reached the maximum number of values"));
        event.cancel = true;
      }
    });
    multiselectText.on('itemAdded itemRemoved', function (event) {
      var values = multiselectText.tagsinput("items");

      if (params.isNumber) {
        values = $.map(values, function (value) {
          // Convert always to . decimal separator for persistence
          value = value.replaceAll(",", ".");
          return self.removeMoneyExtraDecimals(value);
        });
      }

      if (values.length > 0) {
        self.setValueField(params.id, "value", values);
        self.removeValidationValue(params);
      } else
      {
        self.cleanValueField(params.id);
      }
    });


    if (params.isNumber) {
      multiselectText.tagsinput("input").numeric(self.decimalSymbol);
    }
    if (params.item && params.item.value) {
      $.each(params.item.value, function (_, val) {
        multiselectText.tagsinput('add', val);
      });
    }
  },

