/**
 *   Name: Bizagi Workportal Left Sidebar
 *   Author: Elkin Fernando Siabato Cruz
 */
bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.left.sidebar", {}, {

  /**
                                                                                         *   Constructor
                                                                                         */
  init: function init(workportalFacade, dataService, behaviorCollapsiblePanels, params) {
    var self = this;

    // Call base
    self._super(workportalFacade, dataService, params);
    self.behaviorCollapsiblePanels = behaviorCollapsiblePanels;
    //Load templates
    self.loadTemplates({
      "left-sidebar": bizagi.getTemplate("bizagi.workportal.desktop.widget.left.sidebar").concat("#leftsidebar-wrapper") });


    if (bizagi.services.preferences().getPreference("activityCollapseSidebars")) {
      $("#ui-bizagi-wp-project-homeportal-main").addClass("ui-bizagi-wp-project-collapse-leftpanel");
      $("#left-sidebar-wrapper").addClass("always-collpase");
    }

  },

  /**
      * Renders the template defined in the widget
      */
  renderContent: function renderContent() {
    var self = this;
    var template = self.getTemplate("left-sidebar");
    self.content = template.render({});

    return self.content;
  },

  /**
      *
      */
  postRender: function postRender() {
    var self = this;
    self.eventsHandler();
  },

  /**
      * Configure event handlers
      */
  eventsHandler: function eventsHandler() {
    var self = this;
    $(".ui-bizagi-wp-project-panel-collapse", self.content).on("click", $.proxy(self.onCollapsePanel, self));

    self.showTooltip($(".bz-icon-right-outline", self.content), bizagi.localization.getResource("workportal-sidebar-show-more-tooltip"));
    self.showTooltip($(".bz-icon-left-outline", self.content), bizagi.localization.getResource("workportal-sidebar-show-less-tooltip"));

  },
  showTooltip: function showTooltip(classArrow, textTooltip) {
    classArrow.tooltip({
      content: textTooltip,
      tooltipClass: "custom-tooltip-styling-bottom-bar",
      position: {
        my: "left+15 center",
        at: "right top+8",
        of: classArrow } });


  },

  /**
      *
      * @param event
      */
  onCollapsePanel: function onCollapsePanel(event) {
    $('.ui-tooltip.custom-tooltip-styling-bottom-bar').hide();
    var self = this;
    var $target = event ? $(event.currentTarget) : $("#project-collapse-leftpanel");
    var classSuffix = $target.prop("id") === "project-collapse-rightpanel" ? "-rightpanel" : "-leftpanel";

    self.behaviorCollapsiblePanels.addClassesCollapseSideBar(self.content, classSuffix, false);
    $("#left-sidebar-wrapper").removeClass("always-collpase");
    self.publish("openCloseSidebar");
  },

  /**
      *
      */
  clean: function clean() {
    var self = this;
    $(".ui-bizagi-wp-project-panel-collapse", self.content).off("click", $.proxy(self.onCollapsePanel, self));
  } });



bizagi.injector.register("bizagi.workportal.widgets.left.sidebar", ["workportalFacade", "dataService", "behaviorCollapsiblePanels", bizagi.workportal.widgets.left.sidebar], true);