var smartInboxModel = function smartInboxModel() {
  /**
                                                  * Smart Inbox Data Model
                                                  */
  var _smartInboxModel = {
    "id": Math.guid(),
    "ownerType": "user",
    "name": "",
    "resultsPerPage": 50,
    "order": 0,
    "pinned": false,
    "processes": [],
    "columns": [],
    "filter": {},
    "sort": [],
    "access": [] };


  var _getModel = function _getModel() {
    return _smartInboxModel;
  };

  var _setModel = function _setModel(model) {
    if (model == null || model === undefined) return;
    _smartInboxModel = model;
  };
  var _getProperty = function _getProperty(property) {
    return _smartInboxModel[property];
  };
  var _setProperty = function _setProperty(property, value) {
    _smartInboxModel[property] = value;
  };

  var _addColumn = function _addColumn() {
    _smartInboxModel.columns.push({
      "name": "",
      "displayName": "",
      "type": "",
      "from": [
      {
        "process": "",
        "column": "" }] });



  };

  var _removeColumn = function _removeColumn(index) {
    _smartInboxModel.columns.splice(index, 1);
  };

  var _updateColumn = function _updateColumn(index, column) {
    _smartInboxModel.columns[index] = column;
  };

  var _moveColumn = function _moveColumn(fromIndex, toIndex) {
    if (_smartInboxModel.columns[fromIndex] === undefined)
    return;else
    if (_smartInboxModel.columns[toIndex] === undefined) {
      var column = _smartInboxModel.columns[fromIndex];
      _smartInboxModel.columns.splice(fromIndex, 1);
      _smartInboxModel.columns[toIndex] = column;
    } else
    {
      var column = _smartInboxModel.columns.splice(fromIndex, 1)[0];
      _smartInboxModel.columns.splice(toIndex, 0, column);
    }
  };

  var _getColumns = function _getColumns() {
    return _smartInboxModel.columns;
  };

  var _addAccess = function _addAccess() {
    _smartInboxModel.access.push({
      "type": "", //usergroup|role|stakeholder
      "value": "", //guid       
      "displayName": "" });

  };

  var _removeAccess = function _removeAccess(index) {
    _smartInboxModel.access.splice(index, 1);
  };

  var _updateAccess = function _updateAccess(index, access) {
    _smartInboxModel.access[index] = access;
  };

  var _getAccess = function _getAccess() {
    return _smartInboxModel.access;
  };

  var _resetAccess = function _resetAccess() {
    _smartInboxModel.access = [];
  };

  return {
    getModel: _getModel,
    setModel: _setModel,
    get: _getProperty,
    set: _setProperty,
    addColumn: _addColumn,
    removeColumn: _removeColumn,
    updateColumn: _updateColumn,
    moveColumn: _moveColumn,
    getColumns: _getColumns,
    addAccess: _addAccess,
    removeAccess: _removeAccess,
    updateAccess: _updateAccess,
    getAccess: _getAccess,
    resetAccess: _resetAccess };


};


// Auto extend
bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.smartinbox", {}, {

  init: function init(workportalFacade, dataService, params) {
    var self = this;
    self._super(workportalFacade, dataService, params);
    self.context = params.context || "workportal";
    self.dataModel = new smartInboxModel();
    self.processesAvailable = null;
    self.currentStep = 1;
    self.numberOfSteps = params.numberOfSteps || 3;
    self.customizedColumnsAvailable = null;
    self.notifier = bizagi.injector.get('notifier');
    self.loadDataDef = new $.Deferred();
    self.sortOptions = [
    { id: "ascending", text: bizagi.localization.getResource("workportal-widget-smartinbox-ascending", "Ascending") },
    { id: "descending", text: bizagi.localization.getResource("workportal-widget-smartinbox-descending", "Descending") }];

    self.pageSizeOptions = [
    { id: "10", text: "10" },
    { id: "20", text: "20" },
    { id: "30", text: "30" },
    { id: "40", text: "40" },
    { id: "50", text: "50" },
    { id: "100", text: "100" }];

    self.maxColumns = 20;
    // Define editor modes
    if (params.selectedInboxData && params.selectedInboxData.smartInboxId && self.context == "workportal") {
      if (!bizagi.util.isEmpty(params.selectedInboxData.owner)) {
        self.cloning = params.cloning = true;
      } else {
        self.editing = params.editing = true;
      }
    }

    self.filterWidget = new bizagi.workportal.widgets.smartinbox.filter(workportalFacade, dataService, params);

    //Load templates
    self.loadTemplates({
      "smartinbox-main": bizagi.getTemplate("bizagi.workportal.desktop.widget.smartinbox").concat("#ui-bizagi-workportal-widget-smartinbox-wizard"),
      "smartinbox-columns": bizagi.getTemplate("bizagi.workportal.desktop.widget.smartinbox").concat("#ui-bizagi-workportal-widget-smartinbox-columns"),
      "smartinbox-share": bizagi.getTemplate("bizagi.workportal.desktop.widget.smartinbox").concat("#ui-bizagi-workportal-widget-smartinbox-share"),
      "smartinbox-shared-inbox-not-found": bizagi.getTemplate("bizagi.workportal.desktop.widget.smartinbox").concat("#ui-bizagi-workportal-widget-smartinbox-sharedinbox-not-found"),

      useNewEngine: false });


    // Load smart inbox
    if (params.selectedInboxData && params.selectedInboxData.smartInboxId) {
      $.when(self.dataService.getSpecificSmartInbox({
        settingName: params.selectedInboxData.settingName,
        owner: params.selectedInboxData.owner,
        smartInboxId: params.selectedInboxData.smartInboxId })).
      done(function (data) {

        if (!bizagi.util.isEmpty(params.selectedInboxData.owner)) data.id = Math.guid();
        if (!bizagi.util.isEmpty(data.filter)) self.filterWidget.setModel(data.filter);
        if (bizagi.util.isEmpty(data) || data.ownerType !== "user" && data.ownerType !== "default") data.ownerType = "user";

        self.dataModel.setModel(data);
        self.loadDataDef.resolve();
      }).fail(function (err) {
        if (err.status) {
          self.sharedInboxNotFound = true;
          self.loadDataDef.resolve();
        }
      });
    } else {
      self.loadDataDef.resolve();
    }

    self.renderContent();
  },

  /*
     *   Returns the widget name
     */
  getWidgetName: function getWidgetName() {
    return bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_SMARTINBOX;
  },

  /*
     *   Renders the content for the current controller
     *   Returns a deferred because it has to load the current user
     */
  renderContent: function renderContent() {
    var self = this;
    // var template = self.getTemplate("smartinbox");
    var content = self.content = $("<div>");

    return content;
  },

  /**
      *   To be overriden in each device to apply layouts
      */
  postRender: function postRender() {
    var self = this;
    var content = self.getContent();
    var postRenderDef = new $.Deferred();
    var callback;

    $.when(self.loadDataDef).done(function () {
      if (self.sharedInboxNotFound) {
        $.tmpl(self.getTemplate("smartinbox-shared-inbox-not-found")).appendTo(content);
        postRenderDef.resolve();
        $("#bz-btn-smartinbox-close", content).bind("click", $.proxy(self.closeWizard, self));
        return;
      }

      if (self.params.shareMode !== undefined) {
        self.renderShareMode();
        postRenderDef.resolve();
      } else
      {
        callback = self.renderWizardMode();
      }

      $.when(callback).done(function () {
        postRenderDef.resolve();
      });
    });
    return postRenderDef.promise();
  },

  onResize: function onResize() {
    var self = this;
    self.adjustStep2Height();
    self.adjustStep3Height();
  },

  renderWizardMode: function renderWizardMode() {
    var self = this;
    var content = self.getContent();
    var def = new $.Deferred();

    self.mainTemplate = self.getTemplate("smartinbox-main");
    self.columnsTemplate = self.getTemplate("smartinbox-columns");

    $.when(self.loadDataDef).then(function () {

      // Render wizard
      $.tmpl(self.mainTemplate, {
        dataModel: self.dataModel,
        action: typeof self.params.selectedInboxData != "undefined" ? self.params.selectedInboxData : "new",
        editing: self.editing,
        adminMode: self.adminMode,
        numberOfSteps: self.numberOfSteps }).
      appendTo(content);

      // Set rtl
      if (bizagi.localization.isRTL)
      content.addClass("bz-smartinbox-rtl");

      // Load available processes
      $.when(self.dataService.getAllProcessesSmartInbox()).
      then(function (data) {
        self.processesAvailable = data;
        if (self.processesAvailable == null || self.processesAvailable.length == 0) {
          $("#bz-smartinbox-label-warning-processes-notfound").css("display", "block");
          if (self.editing) {
            $("#bz-btn-smartinbox-create").hide();
            $("#bz-btn-smartinbox-cancel").removeClass("bz-btn--secondary");
            $("#bz-smartinbox-label-warning-processes").css("display", "none");
          } else {
            $("#bz-btn-smartinbox-next").hide();
            $("#bz-btn-smartinbox-cancel").show();
          }
        }
        self.loadProcesses(self.processesAvailable);
      });

      self.checkCurrentStep();
      self.validateMode();

      // Render filters
      $.when(self.filterWidget.render()).
      then(function (filterContent) {
        $(".bz-container-filters", self.content).append(filterContent);
        def.resolve();
      });

      // Configure buttons
      $("#bz-btn-smartinbox-back", self.content).bind("click", $.proxy(self.movePrevious, self));
      $("#bz-btn-smartinbox-next", self.content).bind("click", $.proxy(self.moveNext, self));
      $("#bz-btn-smartinbox-cancel", self.content).bind("click", $.proxy(self.closeWizard, self));
      $("#bz-btn-smartinbox-create", self.content).bind("click", $.proxy(self.saveSmartInbox, self));
      $("#bz-smartinbox-label-warning-filters", self.content).bind("click", function () {$(this).hide();});
      $("#bz-smartinbox-label-warning-processes", self.content).bind("click", function () {$(this).hide();});
      $("#bz-smartinbox-label-warning-order", self.content).bind("click", function () {$(this).hide();});
      // Add column handler
      self.content.on("click", "#bz-smartinbox-step-2-add-column", function (e) {
        e.stopPropagation();
        var $form = $("#bz-smartinbox-step2 > form", content);
        //Create a new combo element
        if ($("#bz-smartinbox-step2", content).find(".bz-columns-options").length < self.maxColumns) {
          self.createColumn($form);
        } else
        {
          self.notifier.showErrorMessage(bizagi.localization.getResource("workportal-widget-smartinbox-error-columns-limit", "You have reached the maximum number of columns"));
        }
      });

      if (self.editing) {
        $("#bz-smartinbox-label-warning-processes").css("display", "block");
      }

      self.configureHandlers();
    });

    return def.promise();
  },

  movePrevious: function movePrevious() {
    var self = this;
    if (self.validateWizardAction(self.currentStep, self.currentStep - 1))
    self.moveStepWizard(self.currentStep - 1);
    if (self.currentStep == 1)
    $("#bz-smartinbox-label-warning-processes").css("display", "block");
  },

  moveNext: function moveNext() {
    var self = this;
    if (self.validateWizardAction(self.currentStep, self.currentStep + 1))
    self.moveStepWizard(self.currentStep + 1);
  },

  closeWizard: function closeWizard() {
    var self = this;
    self.params.dialogBox.dialog("close");
  },

  saveSmartInbox: function saveSmartInbox() {
    var self = this;

    // Wait to onchage events set a parsed data
    window.setTimeout(function () {
      self.dataModel.set("filter", self.filterWidget.getPersistenceModel());
      var model = self.dataModel.getModel();

      if (self.editing) {
        if (!self.validateWizardAction(1)) {self.moveStepWizard(1);return;};
        if (!self.validateWizardAction(2)) {self.moveStepWizard(2);return;};
        if (!self.validateWizardAction(3)) {self.moveStepWizard(3);return;};
        if (self.adminMode) {
          if (!self.validateWizardAction(4)) {self.moveStepWizard(4);return;};
        }
      } else {
        if (!self.validateWizardAction(self.currentStep)) return;
      }
      var service;
      if (self.params.selectedInboxData && !self.cloning) {
        service = self.dataService.updateSmartInbox(self.params.selectedInboxData, model);
      } else if (model.ownerType == "user") {
        service = self.dataService.createSmartInbox(model);
      } else {
        service = self.dataService.createAdminSmartInbox(model);
      }

      service.done(function (response) {
        if (response && response.errorCode) {
          if (response.errorCode == "exceededLimit")
          self.notifier.showErrorMessage(bizagi.localization.getResource("workportal-widget-smartinbox-error-exceeded-limit", "This Smart folder cannot be created. You have exceeded the maximum number of Smart folders allowed."));else
          if (response.errorCode == "existingName")
          self.notifier.showErrorMessage(bizagi.localization.getResource("workportal-widget-smartinbox-error-existing-name", "A smart inbox already exists with the name") + " '" + model.name + "'");
        } else
        {
          self.publish("changeWidget", {
            widgetName: bizagi.inboxWidget || bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_INBOX_GRID,
            selectedSmartInboxId: model.id });

          try {
            self.closeWizard();
          } catch (e) {
            $(".ui-dialog-content").dialog("close");
          }
        }
      });
    }, 300);
  },

  moveStepWizard: function moveStepWizard(stepNumber) {
    var self = this;
    self.currentStep = stepNumber;
    self.checkCurrentStep();
  },

  validateMode: function validateMode() {
    var self = this;
    if (self.params.selectedInboxData !== undefined && self.params.selectedInboxData.actionName !== undefined) {
      if (self.params.selectedInboxData.actionName == "create" || self.params.selectedInboxData.actionName == "edit") {
        $("#bz-smartinbox-name", self.content).parent().removeClass('control-required');
        if (self.params.selectedInboxData.actionName == "create")
        $("#bz-smartinbox-name", self.content).val($("#bz-smartinbox-name", self.content).val());
      }
    }
  },

  checkCurrentStep: function checkCurrentStep() {
    var self = this;

    $('#bz-smartinbox-current-step-label', self.content).text(self.currentStep);
    if (self.currentStep == 1) self.checkStepOne();else
    if (self.currentStep == 2) self.checkStepTwo();else
    if (self.currentStep == 3) self.checkStepThree();
  },

  checkStepOne: function checkStepOne() {
    var self = this;

    if (!self.editing) {
      $("#bz-btn-smartinbox-back", self.content).css('display', 'none');
      $("#bz-btn-smartinbox-next", self.content).css('display', '');
      $("#bz-btn-smartinbox-create", self.content).css('display', 'none');
      $("#bz-smartinbox-navigate-label1", self.content).removeClass('bz-inactive-done');
      $("#bz-smartinbox-navigate-label1 > span", self.content).html('1');
    }
    $("#bz-smartinbox-step1", self.content).css('display', '');
    $("#bz-smartinbox-step2", self.content).css('display', 'none');
    $("#bz-smartinbox-step3", self.content).css('display', 'none');
    $("#bz-smartinbox-navigate-label1", self.content).addClass('bz-active');
    $("#bz-smartinbox-navigate-label2", self.content).addClass('bz-inactive');
    $("#bz-smartinbox-navigate-label2", self.content).removeClass('bz-active');
    $("#bz-smartinbox-navigate-label3", self.content).addClass('bz-inactive');
    $("#bz-smartinbox-navigate-label3", self.content).removeClass('bz-active');

    self.updateFilterModel();
  },

  checkStepTwo: function checkStepTwo() {
    var self = this;

    if (!self.editing) {
      $("#bz-btn-smartinbox-back", self.content).css('display', '');
      $("#bz-btn-smartinbox-next", self.content).css('display', '');
      $("#bz-btn-smartinbox-create", self.content).css('display', 'none');
      $("#bz-smartinbox-navigate-label1 > span", self.content).html('<svg style="width:16px;height:16px" xmlns="http://www.w3.org/2000/svg"><use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#check-mark" /></svg>');
      $("#bz-smartinbox-navigate-label2 > span", self.content).html('2');
      $("#bz-smartinbox-navigate-label1", self.content).addClass('bz-inactive-done');
      $("#bz-smartinbox-navigate-label2", self.content).removeClass('bz-inactive-done');
    }

    $("#bz-smartinbox-step1", self.content).css('display', 'none');
    $("#bz-smartinbox-step2", self.content).css('display', '');
    $("#bz-smartinbox-step3", self.content).css('display', 'none');
    $("#bz-smartinbox-navigate-label1", self.content).removeClass('bz-active');
    $("#bz-smartinbox-navigate-label2", self.content).addClass('bz-active');
    $("#bz-smartinbox-navigate-label2", self.content).removeClass('bz-inactive');
    $("#bz-smartinbox-navigate-label3", self.content).addClass('bz-inactive');
    $("#bz-smartinbox-navigate-label3", self.content).removeClass('bz-active');
    self.adjustStep2Height();

    self.loadCustomizedColumns();
    self.updateFilterModel();
  },

  checkStepThree: function checkStepThree() {
    var self = this;

    if (!self.editing) {
      $("#bz-btn-smartinbox-back", self.content).css('display', '');
      $("#bz-btn-smartinbox-next", self.content).css('display', 'none');
      $("#bz-btn-smartinbox-create", self.content).css('display', '');
      $("#bz-smartinbox-navigate-label2 > span", self.content).html('<svg style="width:16px;height:16px" xmlns="http://www.w3.org/2000/svg"><use xmlns:xlink="http://www.w3.org/1999/xlink" xlink:href="#check-mark" /></svg>');
      $("#bz-smartinbox-navigate-label2", self.content).addClass('bz-inactive-done');
    }

    $("#bz-smartinbox-step1", self.content).css('display', 'none');
    $("#bz-smartinbox-step2", self.content).css('display', 'none');
    $("#bz-smartinbox-step3", self.content).css('display', '');
    $("#bz-smartinbox-navigate-label1", self.content).removeClass('bz-active');
    $("#bz-smartinbox-navigate-label2", self.content).removeClass('bz-active');
    $("#bz-smartinbox-navigate-label3", self.content).addClass('bz-active');
    $("#bz-smartinbox-navigate-label3", self.content).removeClass('bz-inactive');
    self.adjustStep3Height();

    self.getAllCustomizedColumnsSmartInbox().
    then(function () {
      var filterModel = self.dataModel.get("filter");
      self.updateFilterModel();
      self.filterWidget.setWFClassList(self.dataModel.get("processes"));
      if (!bizagi.util.isEmpty(self.dataModel.get("processes")) && self.dataModel.get("processes").length > 1) {
        $("#bz-smartinbox-label-warning-filters", self.content).css("display", "block");
        self.filterWidget.setCustomizedColumns(self.buildColumnsForFilterModel(self.customizedColumnsAvailable));
      } else {
        self.filterWidget.setCustomizedColumns(self.customizedColumnsAvailable);
      }

      self.filterWidget.setModel(filterModel);
      self.loadSortingOptions();
    });
  },

  adjustStep2Height: function adjustStep2Height() {
    var self = this;
    var dialogBox = self.params.dialogBox || self.params.masterEditor.params.dialogBox;
    var warningOrder = $("#bz-smartinbox-label-warning-order", self.content).outerHeight(true) || 0;
    $("#bz-smartinbox-step2 form", self.content).css('max-height', dialogBox.height() - 240 - warningOrder + 'px');
  },

  adjustStep3Height: function adjustStep3Height() {
    var self = this;
    var dialogBox = self.params.dialogBox || self.params.masterEditor.params.dialogBox;
    $(".bz-smartfilters-content", self.content).css('max-height', dialogBox.height() - 350 + 'px');
  },

  buildColumnsForFilterModel: function buildColumnsForFilterModel(columns) {
    var self = this;
    return $.grep(columns, function (column) {return !column.from || column.from.length == self.dataModel.get("processes").length;});
  },

  updateFilterModel: function updateFilterModel() {
    var self = this;

    var filterModel = self.filterWidget.getModel();
    if (filterModel != null) {
      self.dataModel.set("filter", filterModel);
    }
  },

  configureHandlers: function configureHandlers() {
    var self = this;
    var content = self.getContent();

    $("#bz-smartinbox-name", content).on("change", function (e) {
      self.dataModel.set("name", e.target.value);

      if (e.target.value.trim() == "")
      $(this).parent().addClass('control-required');else

      $(this).parent().removeClass('control-required');
    });

    // For editing mode, configure tabs
    if (self.editing) {
      $("#bz-smartinbox-navigate-label1", self.content).bind("click", function () {self.changeStepWizard(1);});
      $("#bz-smartinbox-navigate-label2", self.content).bind("click", function () {self.changeStepWizard(2);});
      $("#bz-smartinbox-navigate-label3", self.content).bind("click", function () {self.changeStepWizard(3);});
    }
  },

  changeStepWizard: function changeStepWizard(newStep) {
    var self = this;

    if (self.validateWizardAction(self.currentStep, newStep)) {
      self.moveStepWizard(newStep);
    }
  },

  loadProcesses: function loadProcesses(processesAvailable) {
    var self = this;
    var $search = $("#bz-processes-search", self.content);

    $search.bizagi_multiSelect({
      list: processesAvailable,
      itemValue: function itemValue(obj) {
        return obj.guid;
      },
      itemText: function itemText(obj) {
        return obj.displayName;
      },
      onChange: function onChange(data) {
        self.customizedColumnsAvailable = null;
        var processes = [];
        for (var i = 0; i < data.length; i++) {
          processes.push(data[i].value);
        }

        var columns = self.dataModel.get("columns");
        var defaultColumns = $.grep(columns, function (item) {return item != null && item.isDefault;});

        self.dataModel.set("processes", processes);
        self.dataModel.set("columns", defaultColumns);
        self.dataModel.set("filter", {});
        self.filterWidget.setModel({});
        self.dataModel.set("sort", []);
      },
      label: "",
      iconClass: "bz-icon bz-icon-search-outline bz-icon-16",
      parentPositionElement: "#bz-processes-search > ul",
      parentElementToFitWidth: "#bz-processes-search",
      parentElementToBorder: "#bz-processes-search > ul",
      classOpenedList: "bz-processes-search-border-open-list",
      classOpenedParent: "bz-opened",
      classItem: "theme-navigation-border",
      SEARCH_MIN_LENGTH: 0,
      openOnFocus: true,
      classMenuList: "ui-smart-inbox",
      customRenderItem: function customRenderItem(ul, item) {
        return $("<li>").
        append(
        $("<a>").
        append("<span class='bz-processes-name'>" + item.value.displayName + "</span>").
        append("<span class='bz-processes-category'>" + item.value.application + " - " + item.value.category + "</span>")).

        appendTo(ul);
      } });


    $search.bizagi_multiSelect("setDataItems", self.convertProcessesFromModelToMultiSelect(self.processesAvailable, self.dataModel.get("processes")));
  },

  convertProcessesFromModelToMultiSelect: function convertProcessesFromModelToMultiSelect(allProcess, selectedProcess) {
    var data = [];
    allProcess = allProcess || [];
    selectedProcess = selectedProcess || [];

    for (var i = 0, l = allProcess.length; i < l; i++) {
      if (selectedProcess.indexOf(allProcess[i].guid) > -1) {
        data.push({
          displayName: allProcess[i].displayName,
          guid: allProcess[i].guid });

      }
    }
    return data;
  },

  /**
      * STEP 2: SELECT COLUMNS
      */
  getSelectedProcessesId: function getSelectedProcessesId() {
    var self = this;
    var $search = $("#bz-processes-search", self.content);
    var processesId = [];
    $.each($search.bizagi_multiSelect("getDataItems"), function (_, item) {
      processesId.push(item.value);
    });

    return processesId;
  },

  loadCustomizedColumns: function loadCustomizedColumns() {
    var self = this;
    var content = self.content;
    var processesId = self.getSelectedProcessesId();
    var $form = $("#bz-smartinbox-step2 > form", content);

    $form.sortable({
      items: "> div",
      handle: ".bz-drag",
      start: function start(e, ui) {
        $(this).attr('data-previndex', ui.item.index());
      },
      update: function update(e, ui) {
        var toIndex = ui.item.index();
        var fromIndex = $(this).attr('data-previndex');
        $(this).removeAttr('data-previndex');
        self.dataModel.moveColumn(fromIndex, toIndex);
      } });


    // Invoke service
    self.getAllCustomizedColumnsSmartInbox().
    then(function () {
      var columns = self.dataModel.get("columns");
      var updatedColumns = [];
      if ($form.data("rendered") == "true") $form.empty();

      if (columns.length == 0 || !$.any(columns, function (item) {return item != null && item.alwaysVisible;})) {
        var defaultColumns = $.grep(self.customizedColumnsAvailable, function (item) {return item.isDefault && !item.isOptional;});
        columns = defaultColumns.concat(columns);
      }

      // Add previously configured columns
      for (var i = 0, l = columns.length; i < l; i++) {
        var column = columns[i];
        if (column) {
          if (self.isColumnValid(column, processesId, updatedColumns)) {
            self.createColumn($form, column);
          }
        } else {
          self.createColumn($form);
          updatedColumns.push(null);
        }
      }
      self.dataModel.set("columns", updatedColumns);

      if (updatedColumns.length == 0) {
        if (!self.cloning) {
          // Add first column
          setTimeout(function () {self.createColumn($form);}, 100);
        }
      }

      $form.data("rendered", "true");
      $form.scroll(function (e) {
        $(".ui-select-dropdown.open", $form).remove();
      });
    });
  },

  getAllCustomizedColumnsSmartInbox: function getAllCustomizedColumnsSmartInbox(processesId) {
    var self = this;
    if (!bizagi.util.isEmpty(self.customizedColumnsAvailable)) {
      return $.Deferred().resolve(self.customizedColumnsAvailable).promise();
    }

    var processesId = self.getSelectedProcessesId();
    return $.when(self.dataService.getAllCustomizedColumnsSmartInbox(processesId)).
    then(function (data) {
      self.customizedColumnsAvailable = data;
      return data;
    });
  },


  isColumnValid: function isColumnValid(column, processesId, updatedColumns) {
    var self = this;
    var result = column.isDefault || $.any(column.from, function (item) {return processesId.indexOf(item.process) != -1;});
    if (result) {
      var index = self.customizedColumnsAvailable.findIndex(function (obj) {return obj.name == column.name;});
      if (index > -1) {
        var customizedColumn = self.customizedColumnsAvailable[index];
        column.displayName = customizedColumn.displayName;
        updatedColumns.push(customizedColumn);
      } else {
        result = false;
      }
    }
    return result;
  },

  /**
     * Create a new column 
     */
  createColumn: function createColumn($form, value) {
    var self = this;
    value = value || "";
    var guid = Math.guid();
    var $content = $.tmpl(self.columnsTemplate, {
      guid: guid, alwaysVisible: value.alwaysVisible, displayName: value.displayName });


    if (!value.alwaysVisible) {
      var values = $.grep(self.customizedColumnsAvailable, function (item) {return !item.alwaysVisible;});

      $("#" + guid, $content).uicombo({
        data: { combo: values },
        initValue: value,
        itemValue: function itemValue(obj) {
          return obj.name;
        },
        itemText: function itemText(obj) {
          return obj.displayName;
        },
        onChange: function onChange(obj) {
          var value = obj.ui.data('value');
          var column = $.first(self.customizedColumnsAvailable, function (item) {return item.name == value;});
          if (column != null) {
            var index = $(".bz-columns-options").index($(obj.element).closest(".bz-columns-options"));
            self.dataModel.updateColumn(index, column);
          }
        } });


      $(".bz-delete-column", $content).bind("click", function (e) {
        var index = $(".bz-delete-column").index(this);
        self.dataModel.removeColumn(index);
        $(e.srcElement).closest(".bz-columns-options").remove();
      });
    } else
    {
      $(".bz-delete-column", $content).hide();
    }

    $content.appendTo($form);
    $content[0].scrollIntoView();

    //Add empty column to the model
    if (value === "") {
      var index = $(".bz-columns-options").index($("#" + guid, $form).closest(".bz-columns-options"));
      self.dataModel.updateColumn(index, null);
    }
  },

  /**
      * STEP 3: DEFINE FILTERS / SORT
      */

  loadSortingOptions: function loadSortingOptions() {
    var self = this;
    $(".column-combo-sort", self.content).empty();
    $(".column-combo-order", self.content).empty();
    var selectedValue = null;
    var sort = self.dataModel.get("sort");

    if (sort != null && sort.length > 0 && sort[0].column !== undefined) selectedValue = sort[0];

    self.createComboSortCustomizedColumns($(".column-combo-sort", self.content), selectedValue);

    if (selectedValue == null || !self.isSortColumnValid(selectedValue.column)) {
      self.dataModel.set("sort", []);
      self.createComboEmptySortOptions($(".column-combo-order", self.content));
    } else {
      self.createComboSortOptions($(".column-combo-order", self.content), selectedValue);
    }
  },

  createComboSortCustomizedColumns: function createComboSortCustomizedColumns(customizedColumnItem, sort) {
    var self = this;

    var columns = bizagi.clone(self.dataModel.get("columns"));
    columns.unshift({ name: "", displayName: bizagi.localization.getResource("workportal-widget-smartinbox-please-select-a-value", "Please select a value") });

    var initialValue = columns[0];
    if (sort != null) {
      var search = $.grep(columns, function (item) {return item.name == sort.column;});
      if (search.length > 0) initialValue = search[0];
    }

    $(customizedColumnItem).uicombo({
      data: { combo: columns },
      initValue: initialValue,
      itemValue: function itemValue(obj) {
        return obj.name;
      },
      itemText: function itemText(obj) {
        return obj.displayName;
      },
      onChange: function onChange(obj) {
        if (obj.value == "") {
          $(".column-combo-order", self.content).empty();
          self.createComboEmptySortOptions($(".column-combo-order", self.content));
          self.dataModel.set("sort", []);
        } else
        {
          $(".column-combo-order", self.content).empty();
          self.createComboSortOptions($(".column-combo-order", self.content), sort);
          self.dataModel.set("sort", [{
            "priority": 1,
            "column": obj.value,
            "type": sort ? sort.type : "ascending" }]);

        }
      } });

  },

  createComboSortOptions: function createComboSortOptions(element, sort) {
    var self = this;
    var initialValue = self.sortOptions[0];
    if (sort != null) {
      var search = $.grep(self.sortOptions, function (item) {return item.id == sort.type;});
      if (search.length > 0) initialValue = search[0];
    }

    $(element).uicombo({
      data: { combo: self.sortOptions },
      initValue: initialValue,
      itemValue: function itemValue(obj) {
        return obj.id;
      },
      itemText: function itemText(obj) {
        return obj.text;
      },
      onChange: function onChange(obj) {
        self.dataModel.set("sort", [{
          "priority": self.dataModel.get("sort")[0].priority,
          "column": self.dataModel.get("sort")[0].column,
          "type": obj.value }]);

      } });

  },

  createComboEmptySortOptions: function createComboEmptySortOptions(object) {
    var sortOptions = [
    { id: "", text: bizagi.localization.getResource("workportal-widget-smartinbox-please-select-a-value", "Please select a value") }];


    $(object).uicombo({
      data: { combo: sortOptions },
      initValue: sortOptions[0],
      itemValue: function itemValue(obj) {
        return obj.id;
      },
      itemText: function itemText(obj) {
        return obj.text;
      } });

  },


  validateWizardAction: function validateWizardAction(currentStep, nextStep) {
    var self = this;
    var duplicatedColumns = [];

    if (currentStep > nextStep) {
      return true;
    } else {
      var arrayMessagesErrors = [];
      if (currentStep == 1) {
        if (self.dataModel.get("name").trim() === "") {
          $("#bz-smartinbox-name", self.content).val("");
          arrayMessagesErrors.push(bizagi.localization.getResource("workportal-widget-smartinbox-name-cannot-be-empty", "Name field cannot be empty"));
        }
        if (self.dataModel.get("processes").length == 0) {
          arrayMessagesErrors.push(bizagi.localization.getResource("workportal-widget-smartinbox-at-least-one-process", "At least select one process"));
        }
      } else if (currentStep == 2) {
        // Check if exists empty columns
        var emptyColumns = self.dataModel.get("columns").filter(function (x) {return x === null;});
        if (emptyColumns.length > 0) {
          arrayMessagesErrors.push(bizagi.localization.getResource("workportal-widget-smartinbox-empty-columns", "Can not contain empty columns"));
        } else
        if (self.hasDuplicatedColumns(duplicatedColumns)) {
          arrayMessagesErrors.push(bizagi.localization.getResource("workportal-widget-smartinbox-duplicated-columns", "Can not contain duplicate columns") + ": " + duplicatedColumns.join(", "));
        }
      } else if (currentStep == 3) {
        self.filterWidget.validateFilterForSave(arrayMessagesErrors);
      }
      // Check if exists any error message
      if (arrayMessagesErrors.length > 0) {
        $.each(arrayMessagesErrors, function (ind, elem) {
          self.notifier.showErrorMessage(elem);
        });
        return false;
      } else
      {
        return true;
      }
    }
  },

  validateEmail: function validateEmail(email) {
    var regexEmail = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
    return regexEmail.test(email);
  },

  isSortColumnValid: function isSortColumnValid(columnName) {
    var self = this;
    var columns = self.dataModel.get("columns");
    return $.any(columns, function (item) {return item.name == columnName;});
  },

  hasDuplicatedColumns: function hasDuplicatedColumns(duplicatedColumns) {
    var self = this;
    var columns = self.dataModel.get("columns");
    var result = $.any(bizagi.util.objectValues($.groupBy(columns, "name")), function (item) {
      if (item.length > 1) {
        if (duplicatedColumns.indexOf(item[0].displayName) == -1) {
          duplicatedColumns.push(item[0].displayName);
        }
        return true;
      } else {
        return false;
      }
    });
    return result;
  },

  /*  SHARE FOLDER WIZARD*/
  renderShareMode: function renderShareMode() {
    var self = this;
    var content = self.getContent();

    self.mainTemplate = self.getTemplate("smartinbox-share");
    $.tmpl(self.mainTemplate).appendTo(content);

    // Set rtl
    if (bizagi.localization.isRTL)
    content.addClass("bz-smartinbox-rtl");

    var tagInputEmails = $("#bz-users-search > input", self.content);
    tagInputEmails.tagsinput("items", { allowDuplicates: false, trimValue: true, confirmKeys: [13, 59, 44], delimiter: ";" });
    tagInputEmails.on('beforeItemAdd', $.proxy(self.validateEmailAddress, self));
    $("#bz-users-search", self.content).bind("click", function () {$(this).parent().find('.bootstrap-tagsinput input').focus();});
    $("#bz-btn-smartinbox-share", self.content).bind("click", function () {self.shareSmartInbox(tagInputEmails);});
    $("#bz-btn-smartinbox-cancel-share", self.content).bind("click", function () {self.closeWizard();});
  },

  validateEmailAddress: function validateEmailAddress(event) {
    var self = this;

    if (!self.validateEmail(event.item)) {
      self.notifier.showErrorMessage(bizagi.localization.getResource("workportal-widget-smartinbox-share-invalid-email-address", "Please insert a valid email address"));
      var _this = this;
      setTimeout(function () {
        $(_this).parent().find('.bootstrap-tagsinput input').val(event.item);
        $(_this).parent().find('.bootstrap-tagsinput input').attr("size", event.item.length);
      }, 5);
      event.cancel = true;
    }
  },

  shareSmartInbox: function shareSmartInbox(tagInputEmails) {
    var self = this;
    if (tagInputEmails.tagsinput('items').length > 0) {
      self.dataService.shareSmartInbox(self.params.selectedInboxData, tagInputEmails.tagsinput('items')).done(function (response) {
        if (response === undefined) {
          self.notifier.showSucessMessage(bizagi.localization.getResource("workportal-widget-smartinbox-share-success-message", "Smart Inbox was shared successfully"));
          self.params.dialogBox.dialog("close");
        } else
        {
          if (response.usersNotFound)
          self.notifier.showWarningMessage(bizagi.localization.getResource("workportal-widget-smartinbox-share-usersnotfound", "The following email addresses don't exist:") + " " + response.usersNotFound.join(","));
          if (response.disabledUsers)
          self.notifier.showWarningMessage(bizagi.localization.getResource("workportal-widget-smartinbox-share-disabledusers", "The following email addresses belong to disabled users:") + " " + response.disabledUsers.join(","));
          if (response.currentUser)
          self.notifier.showWarningMessage(bizagi.localization.getResource("workportal-widget-smartinbox-share-currentUser", "Cannot share a smart inbox with itself"));
        }
      });
    } else
    {
      self.notifier.showErrorMessage(bizagi.localization.getResource("workportal-widget-smartinbox-share-message-insert-at-least-one", "Please insert at least one email address"));
    }
  } });