/**
 * Name: Bizagi Workportal Desktop Create plan
 */

bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.user.privacy", {}, {
  /**
                                                                                         * Constructor
                                                                                         */
  init: function init(workportalFacade, dataService, notifier, params) {
    var self = this;

    // Call base
    self._super(workportalFacade, dataService, params);
    self.notifier = notifier;

    //Load template
    self.loadTemplates({
      "user-privacy": bizagi.getTemplate("bizagi.workportal.desktop.widget.user.privacy").concat("#user-privacy") });


  },

  renderContent: function renderContent() {
    var self = this;
    var template = self.getTemplate('user-privacy');
    self.content = template.render({});
    return self.content;
  },

  postRender: function postRender() {
    var self = this;
    $('#js-btn-downloadReport', self.content).on('click', $.proxy(self.onDownloadReport, self));
  },

  onDownloadReport: function onDownloadReport() {
    var self = this;
    self.dataService.downloadReportUser(bizagi.currentUser.idUser);
  },

  clean: function clean() {
    var self = this;
    $('#js-btn-downloadReport', self.content).off();
  } });



bizagi.injector.register('bizagi.workportal.widgets.user.privacy', ['workportalFacade', 'dataService', 'notifier', bizagi.workportal.widgets.user.privacy]);