bizagi.workportal.widgets.widget.extend("bizagi.workportal.widgets.accessDenied", {}, {
  /**
                                                                                         * Constructor
                                                                                         */
  init: function init(workportalFacade, dataService, params) {
    var self = this;

    // Call base
    self._super(workportalFacade, dataService, params);

    //Load template
    self.loadTemplates({
      "access-denied": bizagi.getTemplate("bizagi.workportal.desktop.widget.accessDenied").concat("#access-denied") });

  },

  renderContent: function renderContent() {
    var self = this;
    var template = self.getTemplate('access-denied');
    self.content = template.render({});
    return self.content;
  } });


bizagi.injector.register('bizagi.workportal.widgets.accessDenied', ['workportalFacade', 'dataService', bizagi.workportal.widgets.accessDenied]);