/*
 *   Name: BizAgi Workportal Desktop my search search lists Widget Controller
 *   Author: Andres Fernando Munoz
 *   Comments:
 *   -   This script will provide desktop overrides to implement the my search search lists widget
 */

// Auto extend
bizagi.workportal.widgets.mySearchList.extend("bizagi.workportal.widgets.mySearchList", {}, {

  init: function init(workportalFacade, dataService, params) {
    var self = this;

    // Call base
    self._super(workportalFacade, dataService, params);

    //Load templates
    self.loadTemplates({
      "mySearchListWrapper": bizagi.getTemplate("bizagi.workportal.desktop.widget.mySearchList").concat("#ui-bizagi-workportal-widget-mysearchlist-searchlists-wrapper"),
      "mySearchList": bizagi.getTemplate("bizagi.workportal.desktop.widget.mySearchList").concat("#ui-bizagi-workportal-widget-mysearchlist-searchlists"),
      useNewEngine: false });

  },

  /**
      *   To be overriden in each device to apply layouts
      */
  postRender: function postRender() {
    var self = this;

    self.loadtemplates();
    self.renderSearchLists();
  },

  loadtemplates: function loadtemplates() {
    var self = this;

    self.mySearchListTmpl = self.getTemplate("mySearchList");
  },

  renderSearchLists: function renderSearchLists() {
    var self = this;
    var content = self.getContent();

    $.when(self.dataService.getSearchLists()).done(function (data) {
      var searchLists = data;
      var $searchLists = $.tmpl(self.mySearchListTmpl, { searchLists: searchLists });

      content.append($searchLists);
      self.configureHandlers();
    });
  },

  closeModal: function closeModal() {
    var self = this;
    var modal = $(self.content).closest('.modal');

    modal.hide();
  },

  configureHandlers: function configureHandlers() {
    var self = this;
    var content = self.getContent();

    $(".ui-bizagi-wp-widget-my-search-list-item", content).on('click', $.proxy(self.onListItemClick, self));
    $("#menuQuery").keypress(function () {//Hides the widget when the user types something
      self.closeModal();
    });
  },

  onListItemClick: function onListItemClick(ev) {
    var self = this;
    self.closeModal(); //Hides the widget when the user selects any item on the list
    var $target = $(ev.currentTarget);
    var guidform = $target.data("guidform");
    var id = $target.data("id");
    var displayname = $target.data("displayname");
    var guidEntity = $target.data("guidentity");

    var publishParams = {
      widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_LOADFORM,
      summaryDecorator: false,
      guidForm: guidform,
      displayname: displayname,
      guidEntity: guidEntity,
      closeVisible: false,
      buttons: [
      {
        text: bizagi.localization.getResource('workportal-menu-search'),
        click: function click(e) {
          var that = this;
          var widget = $(this).dialog("option", "widget");
          var form = widget.form || {};

          if (Object.keys(form).length !== 0) {

            if (form && form.validateForm()) {
              var renderValues = [];
              $.forceCollectData = true;
              form.collectRenderValuesQueryForm(renderValues);
              $.forceCollectData = false;

              var filterObject = [];
              var metadataFilters = [];

              $.each(renderValues, function (i, item) {
                if (typeof item.value !== 'function') {
                  var controls = form.getRenders(item.xpath);
                  var control = self.getFilterControl(controls, item);

                  var newValue = control.value;
                  var searchType = control.properties.rangeQuery;
                  if (item && item.advancedQuery) {
                    newValue = item.value;
                    searchType = item.searchType || control.properties.rangeQuery || "NONE";
                  }

                  self.dataService.defineFilterObject({
                    properties: control.properties,
                    value: newValue },
                  filterObject, function (filter) {
                    metadataFilters.push($.extend(filter, {
                      id: control.properties.id,
                      rangeQuery: searchType,
                      displayValue: control.getDisplayValue(),
                      displayName: control.properties.displayName,
                      editable: !!control.properties.editable }));

                  });
                }
              });

              self.performQuery({ guidSearch: id, guidForm: guidform, guidEntity: guidEntity, displayname: displayname, metadataFilters: metadataFilters, filters: filterObject, page: 1, pageSize: 10, calculateFilters: true, histName: displayname, onlyLocalResults: true });
              self.cleanContext();
              $(that).dialog('close');
            }

          }
        } },

      {
        text: bizagi.localization.getResource("workportal-widget-dialog-box-close"),
        click: function click() {
          $(this).dialog('close');
        } }],


      modalParameters: {
        title: displayname } };



    self.publish("showDialogWidget", publishParams);
    self.publish("onWidgetIncludedInDOM");
  },

  performQuery: function performQuery(params) {
    var self = this;
    var dataParams = {
      "type": "SEARCH-ENGINE-VIEW",
      "args": params };


    $.when(self.publish("changeWidget", {
      widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_HOMEPORTAL })).
    done(function () {
      var homeportalInstance = bizagi.injector.get('homeportal');
      var widgetManager = bizagi.injector.get('widgetManager');
      widgetManager._context = "home";

      $.when(homeportalInstance.isReady()).done(function () {
        homeportalInstance.pub("notify", dataParams);
      });
    });
  },

  getFilterControl: function getFilterControl(controls, itemFilter) {
    if (controls.length === 1) {
      return controls[0];
    } else {
      var searchTypeFilter = itemFilter.searchType.toUpperCase();

      for (var index = 0; index < controls.length; index++) {
        var properties = controls[index].properties;
        var searchType = properties.typeSearch || properties.rangeQuery || "NONE";
        searchType = searchType.toUpperCase();

        if (searchType === searchTypeFilter && !bizagi.util.isEmpty(controls[index].value)) {
          return controls[index];
        }
      }

    }
  },
  /**
      * Initialize the context each time a search is done
      */
  cleanContext: function cleanContext() {
    var context = bizagi.injector.get("accumulatedcontext");
    context.clean();
  } });