/*
*   Name: Service to handle the requests for Quick Actions
*   Author: Bizagi
*/

$.Class.extend("bizagi.workportal.services.quickActionsRequester", {
  QUICK_ACTIONS_QUEUE: 'QUICK_ACTIONS_QUEUE' },
{
  /*
  *  Constructor
  */
  init: function init(dataService, params) {
    var self = this;
    self.dataService = dataService;
    self.params = params;
    self.numberAllActions = 0;
    self.executedRequests = 0;
  },


  /**
      * Start a request with specific information
      * @param {object} paramsRequest 
      */
  _startRequest: function _startRequest(paramsQuickAction) {
    var self = this;

    var paramsToBackend = {
      caseId: paramsQuickAction.caseId,
      workItemId: paramsQuickAction.workItemId,
      taskId: paramsQuickAction.taskId,
      quickActionGuid: paramsQuickAction.quickActionGuid,
      actions: self._buildParametersByActions(paramsQuickAction) };


    // necessary to jquery queue()
    return function (next) {

      self.executedRequests++;

      self.params.callback({
        type: 'one-start',
        response: {
          paramsRequest: paramsQuickAction } });



      self.dataService.executeQuickAction(paramsToBackend).done(function (response) {
        self._finishRequest(paramsQuickAction, response);
        next();
      }).fail(function (response) {
        self._finishRequest(paramsQuickAction, response);
        next();
      });
    };
  },

  /**
      * 
      * Receive the notification when a request is success or fail 
      * @param {object} paramsRequest 
      * @param {object} response 
      */
  _finishRequest: function _finishRequest(paramsRequest, response) {
    var self = this;

    self.params.callback({
      type: 'one-end',
      response: {
        paramsRequest: paramsRequest,
        backResponse: response } });


  },

  /**
      * When finish all executions of quick actions
      */
  _finishAllRequests: function _finishAllRequests() {
    var self = this;
    self._clearQueue();
    self.params.callback({
      type: 'all' });

  },

  /**
      * When the user stop all, clear the queue
      */
  _skipPendingRequests: function _skipPendingRequests() {
    var self = this;
    self._clearQueue();
    self.params.callback({
      type: 'stop-by-user',
      response: {
        numberSkipActions: self.numberAllActions - self.executedRequests } });


  },

  /**
      * Clear queue
      */
  _clearQueue: function _clearQueue() {
    var self = this;
    $(document).clearQueue(self.Class.QUICK_ACTIONS_QUEUE);
    $(document).stop();
  },

  /**
      * Build and execute the queue
      * @param {Array} arrayRequestsInformation 
      */
  _executeQueueRequests: function _executeQueueRequests(arrayRequestsInformation) {
    var self = this;

    self.numberAllActions = arrayRequestsInformation.length;

    //fill the queue
    for (var i = 0; i < arrayRequestsInformation.length; i++) {
      $(document).queue(self.Class.QUICK_ACTIONS_QUEUE, self._startRequest(arrayRequestsInformation[i]));
    }

    //start the first element
    $(document).dequeue(self.Class.QUICK_ACTIONS_QUEUE);

    // events
    $(document).queue(self.Class.QUICK_ACTIONS_QUEUE, function () {
      self._finishAllRequests();
    });

  },

  _buildParametersByActions: function _buildParametersByActions(quickAction) {
    var self = this;
    var actions = quickAction.actions;

    var response = [];
    if (quickAction.ruleGuid) {
      response.push(self._buildRequestSubmitData(quickAction.ruleGuid));
      response.push(self._buildRequestCheckpoint(quickAction.ruleGuid));
      response.push(self._buildRequestProcessPropertyValue(quickAction.ruleGuid));
      response.push(self._buildRequestCommit(quickAction.ruleGuid));
    }

    //Next 
    if (JSON.stringify(actions) == '["next","submitData","validate"]') {
      response.push({
        "h_action": "next",
        "h_tag": "performAction" });

    }
    // none
    else if (JSON.stringify(actions) == '["submitData","refresh"]') {
        response.push({
          "h_action": "save",
          "h_tag": "performAction" });

      }
      //next without validation
      else if (JSON.stringify(actions) == '["nextWithoutValidations","submitData"]') {
          response.push({
            "h_action": "nextWithoutValidations",
            "h_tag": "performAction" });

        }
    return response;
  },

  _buildRequestSubmitData: function _buildRequestSubmitData() {
    return {
      "h_action": "SUBMITDATA",
      "h_tag": "submitData" };

  },

  _buildRequestCheckpoint: function _buildRequestCheckpoint(ruleGuid) {
    return {
      "h_action": "CHECKPOINT",
      "h_xpath": "",
      "h_idRender": ruleGuid,
      "h_xpathContext": "",
      "h_tag": "checkpoint" };

  },

  _buildRequestProcessPropertyValue: function _buildRequestProcessPropertyValue(ruleGuid) {
    return {
      "h_action": "PROCESSPROPERTYVALUE",
      "h_idRender": ruleGuid,
      "h_xpathContext": "",
      "h_propertyName": "buttonRule",
      "h_tag": "executeRule" };

  },

  _buildRequestCommit: function _buildRequestCommit(ruleGuid) {
    return {
      "h_action": "COMMIT",
      "h_xpath": "",
      "h_idRender": ruleGuid,
      "h_xpathContext": "",
      "h_tag": "commit" };

  },

  /**
      * Receive the data, transform, and call execution the queue
      * @param {Array} arrayRequestsInformationRaw 
      */
  startAllRequests: function startAllRequests(arrayRequestsInformationRaw) {
    var self = this;

    self._executeQueueRequests(arrayRequestsInformationRaw);
  },

  /**
      * Stop the queue of the requests
      */
  stopAllRequests: function stopAllRequests() {
    var self = this;
    if (self.numberAllActions > self.executedRequests) {
      self._skipPendingRequests();
    }
  } });




bizagi.injector.register('quickActionsRequesterService', ['dataService', bizagi.workportal.services.quickActionsRequester]);