/**
 *   Name: Bizagi Workportal Desktop back Navigator
 */
bizagi.workportal.widgets.navigator.extend("bizagi.workportal.widgets.backnavigator", {

  GO_TO_BACK: function GO_TO_BACK(navigatorInstance) {
    navigatorInstance.defer = $.Deferred();
    if (bizagi && bizagi.referrerParams) {
      var widget = bizagi.referrerParams.referrer || bizagi.util.defaultWidget();

      // switch referer widget
      switch (widget) {
        case "inbox":
          navigatorInstance.publish("changeWidget", {
            widgetName: widget,
            restoreStatus: true });

          bizagi.workportal.actions.routing.LAUNCH_NOTIFICATION();
          break;
        case "inboxGrid":
          navigatorInstance.publish("changeWidget", {
            widgetName: widget,
            restoreStatus: true });

          bizagi.workportal.actions.routing.LAUNCH_NOTIFICATION();
          break;
        case "homeportal":
          navigatorInstance.goToPreviousCrumb();
          bizagi.workportal.actions.routing.LAUNCH_NOTIFICATION();
          break;
        case "search":
          navigatorInstance.publish("executeAction", {
            action: bizagi.workportal.actions.action.BIZAGI_WORKPORTAL_ACTION_SEARCH,
            radNumber: bizagi.referrerParams.radNumber || "",
            page: bizagi.referrerParams.page || 1,
            onlyUserWorkItems: false });

          bizagi.workportal.actions.routing.LAUNCH_NOTIFICATION();
          break;
        case "queryform":
          var params = {
            widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_QUERYFORM,
            queryFormAction: bizagi.referrerParams.queryForm.actionForm,
            notMigratedUrl: "",
            queryParams: bizagi.referrerParams.queryForm.queryParams,
            pageToShow: bizagi.referrerParams.queryForm.pageToShow,
            queryForm: bizagi.referrerParams.queryForm.queryForm };


          $.extend(params, bizagi.referrerParams.queryForm.data, {
            recalculateQueryForm: true });


          params.modalParameters.dialogOpened = navigatorInstance.defer;
          $.when(bizagi.util.autoSave()).done(function () {
            $(document).data('auto-save', '');
            navigatorInstance.publish("showDialogWidget", params);
          });

          navigatorInstance.defer.promise().done(function () {
            bizagi.workportal.actions.routing.LAUNCH_NOTIFICATION();
          });
          break;
        case "reports":
          var params = {
            widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_REPORTS_CHART,
            endPoint: bizagi.referrerParams.reports.endPoint,
            filters: {},
            tab: bizagi.referrerParams.reports.tab };

          bizagi.referrerParams.reports.fromBackButton = true;
          params.modalWasOpened = navigatorInstance.defer;
          navigatorInstance.publish("changeWidget", params);
          break;
        case "folders":
          var url = bizagi.referrerParams.urlParameters + "&page=" + bizagi.referrerParams.page;
          $.when(navigatorInstance.dataService.getCasesByFolder(url)).
          done(function (data) {
            data.customized = true;
            data.urlParameters = bizagi.referrerParams.urlParameters;
            // Define title of widget
            data.title = bizagi.referrerParams.name;
            // Set a flag here to tell the search widget that must show the ungroup case from folder (icon).
            data.casesGroupedByFolder = true;
            data.idFolder = bizagi.referrerParams.id;
            navigatorInstance.publish("changeWidget", {
              widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_SEARCH,
              data: data,
              referrerParams: {} });

          });
          break;
        case "LPQueries":
          bizagi.referrerParams.sidebarData.isFromQueriesBackButton = true;
          navigatorInstance.publish("changeWidget", {
            widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_PROFLY,
            data: {
              route: "reports",
              params: { module: 'none' } } });


          break;
        default:
          navigatorInstance.publish("changeWidget", {
            widgetName: bizagi.workportal.widgets.widget.BIZAGI_WORKPORTAL_WIDGET_INBOX,
            restoreStatus: true });

          break;}

    }
  } },
{});

bizagi.injector.register("backNavigator", [bizagi.workportal.widgets.backnavigator]);