/*
 *   Name: BizAgi Workportal Webpart Controller
 *   Author: Diego Parra
 *   Comments:
 *   -   This script will define a base class to all widgets
 */

/* Asegura que el namespace este creado*/
bizagi = typeof bizagi !== "undefined" ? bizagi : {};
bizagi.webpart = typeof bizagi.webpart !== "undefined" ? bizagi.webpart : {};
bizagi.webpart.subscribe = function (eventName, callback) {
  $(document).bind(eventName, callback);
};
bizagi.webpart.publish = function (eventName, params) {
  return $(document).triggerHandler(eventName, params);
};

bizagi.webpart.whenCreated = function (webpartName, callback) {
  bizagi.webpart.subscribe("bizagi-webpart-created", function (params) {
    if (params.webpart.Class.fullname == "bizagi.workportal.webparts." + webpartName) {
      if (callback) callback(params);
    }
  });
};

bizagi.webpart.whenExecuted = function (webpartName, callback) {
  bizagi.webpart.subscribe("bizagi-webpart-executed", function (params) {
    if (params.webpart.Class.fullname == "bizagi.workportal.webparts." + webpartName) {
      if (callback) callback(params);
    }
  });
};


bizagi.workportal.controllers.controller.extend("bizagi.workportal.webparts.webpart", {},

{
  /*
   *   Constructor
   */
  init: function init(workportalFacade, dataService, params) {
    var self = this;

    // Call base
    this._super(workportalFacade, dataService, params);

    // Set helper
    this.helper = new bizagi.workportal.webpart.helper(this);

    this.canvas = params.canvas;

    // Set project
    this.project = params.project;
    this.sharepointProxyPrefix = params.sharepointProxyPrefix;

    // Set namespace
    this.namespace = params.namespace;
    // visible
    this.isVisible = true;
    this.isWebpartInIFrame = typeof params.isWebpartInIFrame != 'undefined' ? params.isWebpartInIFrame : self.helper.isWebpartInIFrame();
    this.adjustButtonsToContent = params.adjustButtonsToContent;
    this.remoteServer = params.remoteServer;

    if (this.remoteServer) {

      //postmessage object definition
      self.postmessageSocket = new bizagi.postmessage({
        remoteServer: this.remoteServer,
        destination: window.parent,
        origin: window });

      self.postmessageSocket.iframeidentifier = BIZAGI_IFRAME_IDENTIFIER;
      self.postmessageSocket.receive = function (msg) {
        var params = msg.data;

        // Auto-discard self messages
        if (params.message) {
          if (params.message.iframeidentifier == BIZAGI_IFRAME_IDENTIFIER) {
            return;
          }
        }

        if (params.event) {
          switch (params.event) {
            case "postmessage-bizagi-canCloseIFramePopUp":

              if (self.canHide && self.Class.shortName == params.message.webPartDestine) {
                $.when(self.canHide()).
                done(function () {
                  var iframeclosemsg = {};
                  iframeclosemsg.event = "postmessage-bizagi-closeIFramePopUp";
                  iframeclosemsg.message = {};
                  iframeclosemsg.message.iframeidentifier = BIZAGI_IFRAME_IDENTIFIER;
                  self.postmessageSocket.send(iframeclosemsg);
                  var showCasesMSG = {
                    message: {} };

                  showCasesMSG.event = "ui-bizagi-reload-cases";
                  self.postmessageSocket.send(showCasesMSG);
                });
              }
              break;
            case "postmessage-bizagi-openParentPopUp":
              //Do nothing this message is for parent
              break;
            default:
              //re send message to other webparts
              self.publish(params.event, params.message, true);
              break;}

        } else {
          //set iframe height and width

        }
      };
      self.postmessageSocket.startReceiving();
      self.sendDimensionsiFrame();
    }
  },
  sendDimensionsiFrame: function sendDimensionsiFrame(mandatorysend) {
    var self = this;
    //If webpart is put in iFrame resize the iframe with content height
    //else if iframe show in popUp FixButtons, is desirable
    //If namespace is defined this is a chil webpart
    //delay wait for finish render
    if (self.remoteServer) {
      if (!!!self.adjustButtonsToContent && (!!!self.namespace || mandatorysend)) {
        if (self.isWebpartInIFrame) {
          if (self.canvas[0]) {
            //send dimensions in resize event 
            var canvasobject = self.canvas[0];
            if (canvasobject.attachEvent) {
              canvasobject.onresize = function () {
                self.sendDimensionsiFramePostMessage();
              };
            } else {
              bizagi.addResizeListener(canvasobject, function (e) {
                self.sendDimensionsiFramePostMessage();
              });
            }
          }
        }
      }
    }
  },


  sendDimensionsiFramePostMessage: function sendDimensionsiFramePostMessage() {
    var self = this;
    var iframeDimentions = {
      message: {} };

    iframeDimentions.message.iframeName = BIZAGI_IFRAME_IDENTIFIER;
    iframeDimentions.message.iframeHeight = $('body').height();
    if (self.Class.shortName == "newcaselist") {
    } else {
      iframeDimentions.message.iframeWidth = undefined;
    }
    self.postmessageSocket.send(iframeDimentions);
  },

  /**
      *   Publish an event so any controller can subscribe to it
      */
  publish: function publish(eventName, params, notFireToIframe) {
    var self = this;
    eventName = this.namespace ? this.namespace + "." + eventName : eventName;
    //publish event to iframe
    if (!!!notFireToIframe && eventName.indexOf("bizagi-webpart-created") == -1 && eventName.indexOf("bizagi-webpart-executed") == -1 && self.postmessageSocket) {
      var msg = {};
      msg.event = eventName;
      //By iFrame messages it can not transmit the canvas object or object workportal
      if (params) {
        params.canvas = null;
        params.workportal = null;
      }

      msg.message = {};
      msg.message = $.extend(params, {
        project: this.project,
        iframeidentifier: BIZAGI_IFRAME_IDENTIFIER });

      msg.message.whenInitialized = null;
      self.postmessageSocket.send(msg);
    }
    //publish event localy in webparts
    return this._super(eventName, $.extend(params, {
      project: this.project }));

  },

  /**
      *   Subscribe to an event that any controller could trigger
      */
  subscribe: function subscribe(eventName, callback) {
    var self = this;
    eventName = this.namespace ? this.namespace + "." + eventName : eventName;
    this._super(eventName, function (e, params) {
      if (self.project == params.project && self.isVisible)
      return callback(e, params);
    });
  },

  /**
      *   UnSubscribe to an event that any controller could trigger
      */
  unsubscribe: function unsubscribe(eventName, callback) {
    var self = this;
    eventName = this.namespace ? this.namespace + "." + eventName : eventName;
    this._super(eventName, callback);
  },

  /*
      *   Re-draw the webpart
      */
  refresh: function refresh(params) {
    var self = this;
    var content = this.getContent();

    // Clear content
    content.empty();

    // Dispose stuff
    self.prepareForRefresh();

    // Re-draw content;
    $.when(self.render(params)).
    done(function (newContent) {
      if ($.contains(window.document, content[0])) {
        bizagi.util.replaceSelector($("body", window.document), content, newContent);
      } else {
        $("#" + content.attr("id"), window.document).empty();
        bizagi.util.replaceSelector($("body", window.document), "#" + content.attr("id"), newContent);
      }
    });
  },

  /*
      *   Renders the web part
      *   Also ensures that templates are loaded
      */
  render: function render(params) {
    var self = this;

    // Set params in webpart variable
    self.params = params;

    if (params.creating) {
      self.content = self.empty();

      // Trigger webpart created event
      self.publish("bizagi-webpart-created", {
        webpart: self });


      return self.content;
    }

    // Load templates
    return $.when(self.renderContent(params)).
    pipe(function (content) {
      // Then customize in current device
      // if you use fastTmpl the object string is transformed to object html -jquery
      if (typeof content == "string") content = $(content);

      self.content = content;
      self.postRender(params);

      // Trigger webpart executed event
      self.publish("bizagi-webpart-executed", {
        webpart: self });

      self.sendDimensionsiFrame();
      return self.getContent();
    });
  },

  /*
      *   Renders the web part, override this method in each webpart
      */
  renderContent: function renderContent(params) {},

  /*
                                                     *   Customize the web part in each device
                                                     */
  postRender: function postRender(params) {},

  /*
                                               *   Return the rendered content
                                               */
  getContent: function getContent() {
    return this.content;
  },

  /*
      *   Creates an empty content
      */
  empty: function empty() {
    return $("<div/>");
  },

  /*  
      *   Return a specified template
      */
  getTemplate: function getTemplate(template) {
    return this.workportalFacade.getTemplate(template);
  },

  /*  
      *   Create webpart
      */
  createWebpart: function createWebpart(params) {
    return this.workportalFacade.workportal.createWebpart(params);
  },

  /*  
      *   Execute webpart
      */
  executeWebpart: function executeWebpart(params) {
    return this.workportalFacade.workportal.executeWebpart(params);
  },
  /**
      *   Manage the error in base class
      */
  manageError: function manageError(msg, defer, canvasError) {
    var self = this;
    var errorMsg = '';
    if (msg.responseText) {
      errorMsg = msg.responseText;
    } else {
      errorMsg = msg;
    }
    if (errorMsg.indexOf("AUTHENTICATION_ERROR") == -1) {
      self.helper.getFailServerErrorDeferred({
        message: errorMsg,
        sharepointProxyPrefix: self.sharepointProxyPrefix },
      defer);
      self.endWaiting();
      //en caso de no tener un canvasError el error se despliega en todo el content
      if (canvasError) {
        defer.done(function (value) {
          canvasError.append(value);
          self.endWaiting();
        });
      }
      if (bizagi.iframeCommunicator) {
        bizagi.iframeCommunicator.trigger("authorization-internal-error");
      }
      //defer.resolve(msg);
    } else {
      //when it's generate autentication error, cannot load the template
      var content = self.content = "<div class='ui-bizagi-webpart-error-message'>The current user does not exist as a registered user in the bizagi project</div>";
      if (canvasError) {
        canvasError.append(content);
      }
      self.endWaiting();
      defer.resolve(content);
    }
  },
  /**
      *   On waiting. En el area del webpart agrega un div del mismo tamanio y de las mismas coordenadas que el content.
      */
  onWaiting: function onWaiting() {
    var self = this;
    bizagi.startWaiting(document.getElementById(self.canvas[0].id));
  },
  /**
      *   Off waiting. 
      */
  endWaiting: function endWaiting() {
    var self = this;
    self.canvas.children(".ui-bizagi-sharepoint-loading-container").remove();
  },

  /**
      *   Hide WebPart
      */
  hide: function hide(hideParent) {
    var self = this;
    self.canvas.hide();
    if (hideParent) {
      self.canvas.parent().hide();
    }
    self.isVisible = false;
  },
  /**
      *    Show WebPart
      */
  show: function show() {
    var self = this;
    self.canvas.show();
    self.canvas.parent().show();
    self.isVisible = true;
  },
  /**
      *    visible flat
      */
  isVisible: function isVisible() {
    var self = this;
    return self.isVisible;
  },

  destroy: function destroy() {

  },

  prepareForRefresh: function prepareForRefresh() {

  } });