/*
 *   Name: BizAgi Workportal Menu Controller
 *   Author: Diego Parra
 *   Comments:
 *   -   This script will define a base class to handle manu layouts for any device
 */

bizagi.workportal.controllers.controller.extend("bizagi.workportal.controllers.menu", {}, {
  init: function init(workportalFacade, dataService) {
    var self = this;
    self._super(workportalFacade, dataService);

    // Subscribe to change widget event
    this.subscribe("showMainMenu", function (e, params) {
      var def = new $.Deferred();
      $(self.getContent()).closest("#ui-bizagi-wp-menu").show();
      return def.promise();
    });
  },
  /*
     *   Renders the content for the current controller
     *   Returns a deferred because it has to load the current user
     */
  renderContent: function renderContent() {
    var self = this;
    var template = self.workportalFacade.getTemplate("menu");
    var defer = new $.Deferred();
    var defaultImg = "";


    self.customPreferencesObj = new bizagi.services.preferences();
    self.customFormats = new bizagi.services.preferences.customFormats();
    var customPreferencesValues = self.customPreferencesObj.getPreferences();

    /** Manage windows maintenance **/
    var windowMaintenance = new bizagi.workportal.services.windowMaintenance(self.dataService);

    $.datepicker.setDefaults(bizagi.localization.getResource("datePickerRegional"));

    //symbol from Rest/Users/CurrentUser, in server : bizagi.currentUser.symbol =  Business/Localization/Currency Symbol  || {language}.numericFormat.symbol
    var currencyInfo = bizagi.localization.getResource('numericFormat');
    if (currencyInfo) {
      currencyInfo.decimalSymbol = customPreferencesValues.decimalSeparator || bizagi.currentUser.decimalSeparator || currencyInfo.decimalSymbol;
      currencyInfo.symbol = customPreferencesValues.symbol || bizagi.currentUser.symbol || currencyInfo.symbol;
      currencyInfo.digitGroupSymbol = customPreferencesValues.thousandSeparator || bizagi.currentUser.groupSeparator || currencyInfo.digitGroupSymbol;

      bizagi.localization.setResource('numericFormat', currencyInfo);
      if (BIZAGI_DEFAULT_CURRENCY_INFO) {
        BIZAGI_DEFAULT_CURRENCY_INFO.decimalDigits = bizagi.currentUser.decimalDigits;
        BIZAGI_DEFAULT_CURRENCY_INFO.decimalSeparator = currencyInfo.decimalSymbol;
        BIZAGI_DEFAULT_CURRENCY_INFO.symbol = currencyInfo.symbol;
        BIZAGI_DEFAULT_CURRENCY_INFO.groupSeparator = currencyInfo.digitGroupSymbol;
      }
    }

    if (bizagi.currentUser.uploadMaxFileSize) {
      BIZAGI_SETTINGS.UploadMaxFileSize = bizagi.currentUser.uploadMaxFileSize;
      bizagi.util.changeData({ uploadMaxFileSize: BIZAGI_SETTINGS.UploadMaxFileSize });
    }

    self.overrideUserPreferences();

    self.security = new bizagi.workportal.command.security(self.dataService);
    $.when(self.security.getSecurity()).
    done(function (security) {
      // Checking for DisplayOptions into security(response)
      if (bizagi.override.enableFeatureDisplayOptions === false) {
        security.DisplayOptions = false;
      }
      // Render content
      var content = self.content = $.tmpl(template, $.extend(bizagi.currentUser, {
        environment: bizagi.loader.environment || "",
        build: bizagi.loader.build,
        base64image: bizagi.themeLogo || "img/img/barra/logo-bizagi.svg",
        photo: "data:image/png;base64," + (bizagi.currentUser.userPicture ? bizagi.currentUser.userPicture : defaultImg),
        hasPicture: bizagi.currentUser.userPicture ? true : false,
        initials: bizagi.currentUser.userFullName ? bizagi.currentUser.userFullName.getInitials() : "",
        security: security,
        showQrMobileMenu: bizagi.override.showQrMobileMenu !== false }));

      defer.resolve(content);
    });

    return defer.promise();
  },

  overrideUserPreferences: function overrideUserPreferences() {
    var self = this;
    self.customFormats.overrideLocalization();
  },

  /**
      *   Helper method to change the current widget in the workportal
      * @param params
      */
  changeWidget: function changeWidget(params) {
    var self = this;
    self.publish("changeWidget", params);
  },

  /**
      *   Helper method to show a widget inside a dialog in the wokportal
      * @param params
      */
  showDialogWidget: function showDialogWidget(params) {
    var self = this;
    return self.publish("showDialogWidget", params);
  },

  /**
      *
      */
  performResizeLayout: function performResizeLayout() {
    var self = this;
    self.adjustMenuWP();
  },

  adjustMenuWP: function adjustMenuWP() {
    var buttonMenuContainer = $("#ui-bizagi-wp-app-menu-bt-container"),
    /**
                                                                         * add tolerance of an element and after calculate everything restore original size
                                                                         */
    toleranceFor = function toleranceFor(Jelement) {
      var changeWidth = function changeWidth(sign) {
        Jelement.css("width", sign + "=10");
      };
      return {
        start: changeWidth.bind(null, "-"),
        end: changeWidth.bind(null, "+") };

    };
    if (buttonMenuContainer.length === 1) {
      // Define width for elements with responsive
      var elementsMargin = 70, //jquery does not get the margin width with outerWidth :(
      searchInputWidth = $("#ui-bizagi-wp-widget-searchContainer").outerWidth() + 10;
      toleranceFor(buttonMenuContainer).start();
      var widthComponentsWithoutResponsive = searchInputWidth + $("#user").outerWidth() +
      $("#ui-bizagi-wp-app-menu-logo-container").outerWidth() + elementsMargin;

      // Define maximum width of menu
      var limitWidth = $("#ui-bizagi-wp-app-menu-container").width() - widthComponentsWithoutResponsive;
      buttonMenuContainer.width(limitWidth);

      //restart visible buttons
      $("span.text", selectorMenuButtons).show();

      // hide button text by button text until all buttons and text is ok.
      var selectorMenuButtons = $("#ui-bizagi-wp-app-menu-bt-container li");
      var indexButtonToHide = indexLastButton = selectorMenuButtons.length - 1;
      var thereIsAButtonOverlaped = true;

      while (thereIsAButtonOverlaped) {
        if (indexButtonToHide < 0) {
          break;
        }
        if (buttonMenuContainer.position().top > 0 || selectorMenuButtons.eq(indexLastButton).position().top > 0) {
          $("span.text", selectorMenuButtons).eq(indexButtonToHide).hide();
          indexButtonToHide -= 1;
        } else
        {
          toleranceFor(buttonMenuContainer).end();
          thereIsAButtonOverlaped = false;
        }
      }
    }
  } });